<template>
  <div id="myInfo">
      <van-cell-group>
        <van-cell title="头像" value="内容" value-class="avatar">      
            <template slot="default">
              <van-uploader :after-read="modifyAvatar">
                <van-image
                     width="40"
                     height="40"   
                     fit="cover"                   
                     :src="avatar"
                />
              </van-uploader>
            </template>
        </van-cell>
        <van-cell title="姓名" value="我的昵称" value-class="valueClass">
             <template slot="default">
                <van-field v-model="myname" placeholder="我的昵称" class="input" input-align="right"/>
            </template>
        </van-cell>
      </van-cell-group>
      <van-cell-group>
        <van-cell title="设置文章类型" value="健身、情感、生活>>>" />
      </van-cell-group>
      <p class="p1">一定要设置文章类型，获取相关文章推荐</p>
      <van-cell-group>
        <van-cell title="手机" value="13233323232" value-class="valueClass">
           <template slot="default">
                <van-field v-model="myphone" placeholder="13233323232" class="input" input-align="right"/>
            </template>
        </van-cell>
        <van-cell title="上传微信二维码" value=""  >
            <template slot="default">
               <van-uploader :after-read="modifyer">
                <van-image
                     width="26"
                     height="26"   
                     fit="cover"                   
                     :src="erImg"
                />
              </van-uploader>
            </template>
        </van-cell>
      </van-cell-group>
      <p class="p2">如何获取我的二维码图片？</p>
      <van-cell-group>
        <van-cell title="签名" value="个性化" value-class="valueClass">
            <template slot="default">
                <van-field v-model="myqian" placeholder="签名" class="input" input-align="right"/>
            </template>
        </van-cell>
      </van-cell-group>
      <van-cell-group>
          
        <van-cell title="文章顶部显示我的名片和签名" value="" >
             <template slot="title">
                <div class="topshow">文章顶部显示我的名片和签名</div>
            </template>          
            <template slot="default">
                <van-switch v-model="checked1"  size="24px" active-color="#ef5054" inactive-color="#d9d9d9" />
            </template>
        </van-cell>
      </van-cell-group>
      <van-cell-group>
        <van-cell title="文章底部名片显示" value="" >
            <template slot="default">
                <van-switch v-model="checked2"  size="24px" active-color="#ef5054" inactive-color="#d9d9d9" />
            </template>
        </van-cell>
      </van-cell-group>
      <van-cell-group>
        <van-cell title="文章底部显示（换成我的名片文章）" value="" >
             <template slot="title">
                <div class="topshow">文章底部显示（换成我的名片文章）</div>
            </template>
            <template slot="default">
                <van-switch v-model="checked3"  size="24px" active-color="#ef5054" inactive-color="#d9d9d9" />
            </template>
        </van-cell>
      </van-cell-group>
      <p class="p3">(换成我的名片文章) 可以帮你锁定客户，<span>查看详情</span></p>
      <p class="p4">个人信息会显示在名片上，<span> 查看我的名片</span></p>
  </div>
</template>

<script>
import 'vant/lib/switch/style'
export default {
  name: 'myInfo',
  data () {
    return {
      checked1: false,
      checked2: true,
      checked3: true,
      myname:'',
      myphone:'',
      myqian:'',
      avatar: 'https://img.yzcdn.cn/vant/cat.jpeg',
      erImg:'https://linkme.oss-cn-beijing.aliyuncs.com/share/i/er.png'
    }
  },
  methods: {
    modifyAvatar(file){
        this.avatar = file.content
    },
    modifyer(file){
      this.erImg = file.content
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    #myInfo{
        position: absolute;
        left:0;
        top:0;
        width:100%;
        height:100%;
        background:#fff; 
    }
    .van-cell__title{
        text-align: left;
        font-size:16px
    }
    .van-cell-group{
        margin-bottom:8px
    }
    p{
        color:#999;
        font-size:12px
    }
    .p1,.p2{
        text-align: right;
        background:#eee;
        padding:10px 10px 10px 0
    }
    .p3{
        padding:0;
        margin-bottom:-10px
    }
    .p3 span{
        color:#ef5054
    }
    .p4 span{
        color:#ef5054
    }
    .topshow{
        min-width: 260px
    }
    .avatar .van-image{
      overflow:hidden;
      border-radius:50px;
    }
    .valueClass{
        width: 32%;
        position: absolute;
        right: 0;
        top: 0px;
    }

</style>



