                </div>
            </div>
            <article>
                <p>大家纷纷表示，习近平总书记的重要指示，将推动全社会形成见贤思齐、甘于奉献的良好氛围，要深入学习贯彻习近平总书记重要指示精神，以张富清为榜样，积极弘扬奉献精神不忘初心、牢记使命，淡泊名利，砥砺前行，努力做新时代的奋斗者。</p>
