                　　“保持信念之光不灭，信仰之树常青”
                　　习近平总书记在重要指示中强调，老英雄张富清60多年深藏功名，一辈子坚守初心、不改本色，事迹感人。</p>
                <p><img src="https://sikeuser.news.cn/shortvideo/fmtimg/86.jpg" alt=""></p>
                <p>江西，是一片充满红色记忆的红土地。中央红军长征从这里集结出发，广大人民群众与党同心，用鲜血打拼出一番新的天地……</p>
