<script>
export default {
    data () {
      return {
          show:false,
          id: this.$route.query.id
          
      }
  },
  methods: {
      jumptochange(){
          this.$router.push({path: '/EditCard'})
      },
      jumptoedit(){
          this.$router.push({path: '/modify'})
      },
      telto(){
          window.location.href='tel://18911201110'
      },
      weixin(){
          this.show = true
      },
      backhome(){
          this.$router.push({path: '/mycenter'})
      },
      linkhe(){
          console.log(this.$el.querySelector("#card").offsetTop)
          this.$el.querySelector("#card").scrollIntoView();
      },
      tousu(){
          this.$router.push({path: '/tousu'})
      },
      backbtn(){
          this.$router.push({path: '/'})
      }
  },
  mounted () {
      console.log(this.$route.params && this.$route.params.id)
      document.title="详情页面具体标题"
  }
}
</script>


<style scoped>
    #ArticleDetail{
        position:absolute;
        width:100;
        top:0;
        left:0;
        background:#f2f2f2;
        overflow: hidden;
       
