        width:98%;
        height:66px;
        margin:0 auto;
        border-radius: 10px;
        box-shadow: 0 0 15px  #d9dadc;
    }
    .avatar{
         position:absolute;
         left:10px;
         top:15px;
         width:34px;
    }
    .avatar img{
         width:100%
    }
    .name{
        position:absolute;
        left:50px;
        top:22px;
        font-size:14px
    }
    .authorRight{
        position: absolute;
        right:10px;
        top:20px;
        font-size:10px;
        
    }
    article{
        padding-bottom:50px;
        margin-bottom:80px;
    }
    .tousu{
        position: absolute;
        bottom:10px;
        right:10px;
        color:#586b95
    }
    .card{
        position: relative;
        width:92%;
        margin:0 4%;
        height:260px;
        background:#fff;
        border-radius:10px;
        margin-bottom:50px;
    }
    .avatarCard{
        width:70px;
        margin-top:-35px;
        padding:3px;
        background:#fff;
        border-radius: 100%;
        margin-bottom:10px;
