    }
    .brief h3{
        text-align: left;
        border-left: #ef5054 solid 3px;
        padding-left:10px;
        margin-left:10px;
        font-size:16px
    }
    .brief p{
        text-align: left;
        padding:0 20px;
        font-size:14px;
        line-height: 1.5;
        color:#999
    }
    .changeBtn{
        position: absolute;
        padding:4px 14px;
        background:#e3e3e3;
        color:#666666;
        font-size:14px;
        top:20px;
        right:0;
        border-radius: 20px 0 0 20px
    }
    .editBtn{
        position: absolute;
        padding:10px 20px;
        background:#ef5054;
        color:#fff;
        font-size:15px;
        bottom:-10px;
        right:-5%;
        border-radius: 20px 0 0 20px;
        box-shadow: 0 0 10px  #ef5054;
    }
    .backbtn{
        width:40px;
        height:40px;
        padding:0;
        min-width:40px;
        position:fixed;
        right:10px;
        bottom:100px;
    }
    .van-icon__image{
        width:15px
    }
</style>




