<template>
  <div class="writeArticle">
      <ul>
          <li @click="writeArticle">
              <img src="../assets/write.png" alt="" class="slt">
              <h2>写文章</h2>
              <p class="des">自由添加文字、图片</p>
              <img src="../assets/icon_jiantou.png" alt="" class="arrow"> 
          </li>
          <li @click="copyArticle">
              <img src="../assets/copy.png" alt="" class="slt">
              <h2>复制文章</h2>
              <p class="des">发送原文链接即可复制</p>
              <img src="../assets/icon_jiantou.png" alt="" class="arrow"> 
          </li>
          <li @click="draftBox">
              <img src="../assets/draft.png" alt="" class="slt">
              <h2>草稿箱（0）</h2>
              <p class="des">未编辑完成的文章在这里</p>
              <img src="../assets/icon_jiantou.png" alt="" class="arrow"> 
          </li>
          <li @click="myArticle">
              <img src="../assets/myarticle.png" alt="" class="slt">
              <h2>我的文章（4）</h2>
              <p class="des">写过的、分享的文章都在这里</p>
              <img src="../assets/icon_jiantou.png" alt="" class="arrow"> 
          </li>
      </ul>
  </div>
</template>

<script>
export default {
  name: 'writeArticle',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  methods: {
      writeArticle(){
          this.$router.push({path: '/edit'})
      },
      copyArticle(){
          this.$router.push({path: '/copyArticle'})
      },
      draftBox(){
          this.$router.push({path: '/draftBox'})
      },
      myArticle(){
          this.$router.push({path: '/myArticle'})
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .writeArticle{
        position: absolute;
        width:100%;
        height:100%;
        background:#f1f1f1;
    }
    ul{
        position: relative;
        width: 90%;
        margin:0 auto
    }
    ul li{
        position: relative;
        background-color:#fff;
        margin-top:20px;
        border-radius: 10px;
        width: 100%;
        height:100px;
    }
    ul li .slt{
        width: 60px;
        height: 60px;
        position: absolute;
        left:10px;
        top:50%;
        margin-top:-30px;
    }
    ul li h2{
        position: absolute;
        left:90px;
        font-size:18px;
        top:10px;
    }
    ul li .des{
        position: absolute;
        left:90px;
        top:36px;
        color:#ababab
    }
    ul li .arrow{
        position: absolute;
        right:10px;
        top:50%;
        margin-top:-10px;
        width:20px;
    }
</style>



