<template>
  <div id="MessageSet">
      <van-cell-group class="group1">
        <van-cell>
            <template slot="title">
                <div class="title1">谁看我消息提醒设置</div>
            </template>
        </van-cell>
        <van-radio-group v-model="radio">
            <van-cell-group>
                <van-cell title="立即提醒我" clickable @click="radio = '1'">
                     <template slot="title">
                         <div class="title">立即提醒我</div>
                     </template>
                    <van-radio name="1" checked-color="#ef5054"/>
                </van-cell>
                <van-cell title="15分钟内只提醒一次（汇总提醒）" clickable @click="radio = '2'">
                    <template slot="title">
                         <div class="title">15分钟内只提醒一次（汇总提醒）</div>
                     </template>
                    <van-radio name="2" checked-color="#ef5054"/>
                </van-cell>
                <van-cell title="1小时内只提醒一次（汇总提醒）" clickable @click="radio = '3'">
                     <template slot="title">
                         <div class="title">1小时内只提醒一次（汇总提醒）</div>
                     </template>
                    <van-radio name="3" checked-color="#ef5054"/>
                </van-cell>
                <van-cell title="3小时内只提醒一次（汇总提醒）" clickable @click="radio = '4'">
                     <template slot="title">
                         <div class="title">3小时内只提醒一次（汇总提醒）</div>
                     </template>
                    <van-radio name="4" checked-color="#ef5054"/>
                </van-cell>
                <van-cell title="关闭提醒" clickable @click="radio = '5'">
                    <template slot="title">
                         <div class="title">关闭提醒</div>
                     </template>
                    <van-radio name="5" checked-color="#ef5054"/>
                </van-cell>
            </van-cell-group>
         </van-radio-group>
      </van-cell-group>
      <van-cell-group class="group2">
        <van-cell title="" >
            <template slot="title">
                <div class="title">夜间模式<span>(北京时间23:00-6:00)</span></div>
            </template>
            <template slot="default">
                <van-switch v-model="checked1"  size="24px" active-color="#ef5054" inactive-color="#d9d9d9" />
            </template>
        </van-cell>
      </van-cell-group>
      <van-cell-group class="group3">
        <van-cell>
            <template slot="title">
                <div class="title1">热门文章推送设置</div>
            </template>
        </van-cell>
        <van-cell>
             <template slot="title">
                <div class="title">早安推送<span>（6:00-7:00推送）</span></div>
            </template>
            <template slot="default">
                <van-switch v-model="checked2"  size="24px" active-color="#ef5054" inactive-color="#d9d9d9" />
            </template>
        </van-cell>
        <van-cell >
             <template slot="title">
                <div class="title">热文推送<span>（8:00-9:00推送）</span></div>
            </template>
            <template slot="default">
                <van-switch v-model="checked3"  size="24px" active-color="#ef5054" inactive-color="#d9d9d9" />
            </template>
        </van-cell>
        <van-cell >
             <template slot="title">
                <div class="title">热文推送<span>（9:00-10:00推送）</span></div>
            </template>
            <template slot="default">
                <van-switch v-model="checked4"  size="24px" active-color="#ef5054" inactive-color="#d9d9d9" />
            </template>
        </van-cell>
        <van-cell >
             <template slot="title">
                <div class="title">热文推送<span>（14:00-15:00推送）</span></div>
            </template>
            <template slot="default">
                <van-switch v-model="checked5"  size="24px" active-color="#ef5054" inactive-color="#d9d9d9" />
            </template>
        </van-cell>
      </van-cell-group>
      <van-cell-group class="group4">
        <van-cell>
             <template slot="title">
                <div class="title">每日数据报告</div>
            </template>
            <template slot="default">
                <van-switch v-model="checked6"  size="24px" active-color="#ef5054" inactive-color="#d9d9d9" />
            </template>
        </van-cell>
         <van-cell>
             <template slot="title">
                <div class="title">晚安推送<span>(21:00-22:00推送)</span></div>
            </template>
            <template slot="default">
                <van-switch v-model="checked7"  size="24px" active-color="#ef5054" inactive-color="#d9d9d9" />
            </template>
        </van-cell>
      </van-cell-group>
      <p class="p1">如果已经开启“热门文章推送”，仍未收到文章。请回到公众号，点击“客户”</p>
  </div>
</template>

<script>
import 'vant/lib/switch/style'
export default {
  name: 'MessageSet',
  data () {
    return {
      radio: '1',
      checked1: true,
      checked2: false,
      checked3: true,
      checked4: true,
      checked5: true,
      checked6: true,
      checked7: true
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    #MessageSet{
        position: absolute;
        left:0;
        top:0;
        width:100%;
        height:100%;
        background:#f2f2f2;
    }
    .van-cell__title{
        text-align: left;
        font-size:16px
    }
    .van-cell-group{
        margin-bottom:8px
    }
    p{
        color:#999;
        font-size:12px
    }
    .p1,.p2{
        text-align: left;
        padding:5px 10px
    }
    .p3{
        padding:0;
        margin-bottom:-10px
    }
    .p3 span{
        color:#ef5054
    }
    .p4 span{
        color:#ef5054
    }
    .title1{
        font-size:20px;
        font-weight: 600;
        padding:6px 0
    }
    .group1 .van-cell__value{
        position:absolute
    }
    .group1 .title{
        padding-left:30px
    }
    .group2 .van-cell__value{
        position: absolute;
        right:10px
    }
    .group2 .title{
        padding-top:2px
    }
     .group3 .van-cell__value{
        position: absolute;
        right:10px
    }
    .group3 .title{
        padding-top:2px
    }
     .group4 .van-cell__value{
        position: absolute;
        right:10px
    }
    .group4 .title{
        padding-top:2px
    }
</style>



