<template>
    <div id="modify">
        <div class="mask" v-if="helpmask" @click="helpmaskfn">
            <p class="title">想改哪里<span>点</span>哪里</p>
            <img src="../assets/helptips.png" alt="" class="helphand">
            <van-button class="notips" @click="notips">不再提示</van-button>
            <van-button class="iknow" @click="iknow">我知道了</van-button>
        </div>
        <div class="header">
            <van-cell :title="title"
                    value-class="listImg"
                    title-class="listTitle" >
                <template slot="default">
                    <van-image width="80" height="80" fit="cover" :src="fm" />
                </template>
                <template slot="title">
                    <div v-on:click.stop="toggleTooltip" v-if="!show_tooltip">{{ title }}</div>
                    <div class="tooltop" v-on:click.stop v-else style="width:100%;"> 
                        <textarea  rows="2" type="text"  v-model="title" style="width:100%;"></textarea>
                    </div>
                </template>            
            </van-cell>               
            <div>
                <van-button plain type="danger" round icon="
https://linkme.oss-cn-beijing.aliyuncs.com/share/i/txt.png" size="small" class="btn" @click="modifyTitle" v-if="!show_tooltip">修改标题</van-button>
                <van-button plain type="danger" round icon="
https://linkme.oss-cn-beijing.aliyuncs.com/share/i/txt.png" size="small" class="btn" @click="modifyTitle" v-if="show_tooltip">保存</van-button>
                <van-uploader :after-read="afterUpLoad">
                    <van-button plain type="danger" round icon="
https://linkme.oss-cn-beijing.aliyuncs.com/share/i/img.png" size="small" class="btn">修改封面</van-button>
                </van-uploader>
            </div>
        </div>

         <article id="articleDetail">
            <p @click="modifyCon(index,$event)" v-for = "(item, index)  in articlecon" :key="index" v-html="item" ></p>

        </article>
        <div  v-if="!show_content_tooltip">
            <textarea v-model="content" :style="{ width: tawidth+'px', height: taheight+'px', top: tatop+'px', left:taleft+'px' }" class="contextarea" ></textarea>
            <div class="txtimg" :style="{ top: (taheight+tatop)+'px', left:taleft+'px',width: tawidth+'px' }" >
                <van-button plain type="danger" round icon="
https://linkme.oss-cn-beijing.aliyuncs.com/share/i/txt.png" size="small" class="btn" @click="saveCon">保存</van-button>
                    <!-- <van-button plain type="danger" round icon="https://img.yzcdn.cn/vant/logo.png" size="small" class="btn" >图片</van-button> -->
            </div>
       </div>
        <div  v-if="!show_content_tooltipimg">         
            <div class="txtimg2" :style="{ top: (taheight+tatop)+'px', left:taleft+'px',width: tawidth+'px' }" >
                 <van-uploader :after-read="afterUpLoadCon">
                    <van-button plain type="danger" round icon="
https://linkme.oss-cn-beijing.aliyuncs.com/share/i/img.png" size="small" class="btn">替换图片</van-button>
                 </van-uploader>
                    <!-- <van-button plain type="danger" round icon="https://img.yzcdn.cn/vant/logo.png" size="small" class="btn" >图片</van-button> -->
            </div>
       </div>    
        <div class="bottomBtn">
            <van-button  type="danger" square  icon="https://img.yzcdn.cn/vant/logo.png" size="large" class="cancel" @click="cancelModify">放弃修改</van-button>
            <van-button  type="danger"  square size="large" class="save" @click="saveModify">保存</van-button>
        </div>
    </div>
</template>

<script>
export default {
    name: 'modify',
    data(){
        return{
            show_tooltip: false,
            show_content_tooltip: false,
            show_content_tooltipimg: false,
            helpmask:true,
            title: '习近平向2019中国国际大数据产业博览会致贺信',
            content:'',
            tawidth:'',
            taheight:'',
            tatop:'',
            taleft:'',
            fm:'https://img.yzcdn.cn/vant/cat.jpeg',
            articlecon: {
                "0":"大家纷纷表示，习近平总书记的重要指示，将推动全社会形成见贤思齐、甘于奉献的良好氛围，要深入学习贯彻习近平总书记重要指示精神，以张富清为榜样，积极弘扬奉献精神不忘初心、牢记使命，淡泊名利，砥砺前行，努力做新时代的奋斗者。",
                "1":"致敬英雄，保持赤诚之心“保持信念之光不灭，信仰之树常青”习近平总书记在重要指示中强调，老英雄张富清60多年深藏功名，一辈子坚守初心、不改本色，事迹感人。",
                "2":"<img src='https://sikeuser.news.cn/shortvideo/fmtimg/86.jpg' alt=''>",
                "3":"江西，是一片充满红色记忆的红土地。中央红军长征从这里集结出发，广大人民群众与党同心，用鲜血打拼出一番新的天地……",
                "4":"江西，是一片充满红色记忆的红土地。中央红军长征从这里集结出发，广大人民群众与党同心，用鲜血打拼出一番新的天地……",
                "5":"<div>江西，是一片充满红色记忆的红土地。中央红军长征从这里集结出发，广大人民群众与党同心，用鲜血打拼出一番新的天地……</div>",
            },
            contentId:'0'
        }
    },
    methods: {
        modifyTitle(){
            this.show_tooltip = !this.show_tooltip
        },
        afterUpLoad(file){
            console.log(file)
            this.fm = file.content
        },
        modifyCon(id,e){       
            if(e.path[0].src){
                this.show_content_tooltip = true
                this.show_content_tooltipimg = false
                this.contentId = id
                this.tawidth = e.path[0].offsetWidth
                this.taheight = e.path[0].offsetHeight
                this.tatop = e.path[0].offsetTop
                this.taleft = e.path[0].offsetLeft
                console.log('图片')
            }else if(e.path[0].innerHTML){
                this.show_content_tooltipimg = true
                this.show_content_tooltip = false
                 console.log(id)
                 console.log(e.path[0].offsetTop,e.path[0].offsetHeight)
                 this.contentId = id
                 this.tawidth = e.path[0].offsetWidth
                 this.taheight = e.path[0].offsetHeight
                 this.tatop = e.path[0].offsetTop
                 this.taleft = e.path[0].offsetLeft
                 this.content = e.path[0].innerHTML
            }
        },
        saveCon(e){
            this.show_content_tooltip = !this.show_content_tooltip
            this.articlecon[this.contentId] = this.content
            console.log(this.articlecon[this.contentId])
        },
        afterUpLoadCon(file){
             this.articlecon[this.contentId] = "<img src='"+file.content+"'/>"
             console.log(file)
        },
        cancelModify(){
            this.$dialog.alert({
            message: "确定放弃修改？",
            showCancelButton: true
        })
        .then(()=>{
             this.$router.go(-1)
        })
        .catch(()=>{
            console.log("取消")
        })       
        },
        saveModify(){
            this.$router.push({path: '/detail'})
        },
        helpmaskfn(){
            this.helpmask = false
        }
    }
}
</script>

<style scoped>
#modify{
    position: absolute;
    left:0;
    top:0;
    width:100%;
    height: 100%;
    background:#f2f2f2
}
.header{
    background:#e4e4e4;
    padding:6px 10px;
}
.header .van-cell{
    min-height: 100px;
}
.listTitle{
    text-align: left;
    min-height:40px;
    font-size:16px;
    line-height: 1.5;
    position:absolute;
    width:70%;
}
.listImg{
    width:80px;
    height: 80px;
    display: block;
    position:absolute;
    right:0;
    border-radius: 4px;
    overflow: hidden;
}
.van-cell{
    padding:10px 4px;
    background:#e4e4e4;
    border-bottom: #c6c6c6 solid 1px
}
.van-cell__title, .van-cell__value {
    flex:auto
}
.btn{
    margin-right:10px;
    margin-top:4px;
    border:none;
    background:none;
    font-size:15px;
}
.txtimg{
    position: absolute;
    margin:0 auto;
    text-align: center;
    background:#cecdcd;
}
.txtimg .btn{
    padding:0 0
}
.txtimg2{
    position: absolute;
    margin:0 auto;
    text-align: center;
    background:#cecdcd;
}
.txtimg2 .btn{
    padding:0 0
}
.appendtxtarea{
   box-shadow: 0 0 4px  #cecdcd;
   margin:0 auto;
   margin-bottom:20px;
   padding-bottom:20px;
   width:94%;
   border-radius: 10px 10px 0 0;
  
}
.textarea{
    width:95%;
    border-radius: 10px;
    border:none;
    padding:10px 10px;
    box-shadow: 0 0 8px  #cecdcd
}
.bottomBtn{
    position: fixed;
    bottom:0px;
    left:0;
    width:100%;
    display: flex
}

.bottomBtn .cancel{
    width:100%;
    padding:0;
    border:none;
    background:#d9d9d9;
    color:#000;
    font-size:18px;
}
.bottomBtn .save{
    width:100%;
    padding:0;
    border:none;
    font-size:18px;
}
article{
    padding:10px 20px 10px 20px;
    text-align: left;
    line-height: 1.6;
    margin-bottom:100px;
}
article >>> img{
        width:90% !important;
        margin:0 auto !important;
        display: block !important
}
.contextarea{
    position: absolute;
}
textarea{
    padding:0;
    border:none
}
.mask{
    position: fixed;
    left:0;
    top:0;
    width:100%;
    height:100%;
    background:rgba(0,0,0,0.7);
    z-index: 3;  
    color:#fff;
    text-align: center; 
    overflow: hidden;
}
.mask .helphand{
    position: absolute;
    left:50%;
    top:22%;
    width:80%;
    margin-left:-40%;
}
.mask .title {
    position: absolute;
    top: 5%;
    width: 100%;
    text-align: center;
    color: #fff;
    font-size: 30px;
}
.mask .title span {
    padding-left: 20px;
    color: #ffb41e;
    font-weight: 700
}
.mask .notips {
    position: absolute;
    bottom: 18%;
    left: 50%;
    width: 120px;
    margin-left: -135px;
    background:transparent;
    color:#fff;
    font-size: 16px;
    border-radius: 4px
}
.mask .iknow {
    position: absolute;
    bottom: 18%;
    left: 50%;
    width: 120px;
    margin-left: 25px;
    background: #ed464a;
    color:#fff;
    font-size: 16px;
    border:none;
    border-radius: 4px
}
</style>



