<template>
  <div class="TxtEdit">
    <van-cell-group>
        <van-field v-model="content" type="textarea" placeholder="请输入文字" rows="6" autosize  class="inputTxt"/>
    </van-cell-group>
    <van-checkbox v-model="checked" class="saveRadio" checked-color="#07c160">存为常用</van-checkbox>
    <van-cell-group class="btnGroup">
        <van-button color="#7232dd" round class="cancelBtn" @click="cancelBtn">取消</van-button>
        <van-button color="#7232dd" round class="okBtn" @click="okBtn">确定</van-button>
    </van-cell-group>
    <div class="line"></div>
    <p class="cy">常用文字</p>
    <p class="bj">编辑</p>
    <img src="../assets/icon_nrbianji.png" alt="" class="bjimg">
    <ul>
        <li v-for="(item,index) in list"
                    :key="item.name"
                    :name="item">   
        <van-button plain  size="small" @click="choosebrand(index)">{{ item.value }}</van-button> 

        </li>
    </ul>
    

  </div>
</template>

<script>

export default {
  name: 'TxtEdit',
  data () { 
    return {
        content: '',
        checked:true,
        list: [
              {"name":"0","value":"文字1"},
              {"name":"1","value":"文字fadsfasdfasd2"},
              {"name":"2","value":"文字3"},
              {"name":"3","value":"文字4"},
              {"name":"4","value":"文字5"},
              {"name":"5","value":"文字6"},
              {"name":"6","value":"文字7"},
              {"name":"7","value":"文字8"},
              {"name":"8","value":"文字9"}
          ]
    }
  },
  props:['psMsg'],
  methods: {
     okBtn() {
         this.$emit('transfer',this.content)//将值绑定到transfer上传递过去
         //this.$router.push({path: '/edit'})
     },
     cancelBtn() {
        this.$emit('transfer','')
     }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .TxtEdit {
        position: fixed;
        left:0;
        top:0;
        width:100%;
        height:100%;
        background:#fff;
        overflow: hidden
    }
    .inputTxt {
        background: #f4f3f3;
        width: 90%;
        margin:20px auto
    }
    .saveRadio {
        float:right;
        padding-right:20px;
        z-index: 10;
    }
    .btnGroup {
        margin:60px auto 10px auto;
        z-index: 1;
    }
    .cancelBtn {
        width: 40%;
        margin-right:20px;
        background-color: #e6e6e6;
    }
    .okBtn {
        width: 40%;
        background-color: #EF5054;
        color: #fff
    }
    .line {
        width: 100%;
        height:14px;
        background-color: #d6d6d6
    }
    .cy {
        float:left;
        padding-left:10px;
        color:#999;
    }
    .bjimg {
        float: right;
        padding:18px 6px 0 0;
        width: 20px;
    }
    .bj {
        float: right;
        padding-right:10px;
        color:#EF5054
    }
    ul {
        width: 100%;
        margin:60px 0 0 10px;
        text-align: left
    }
    li{
        list-style-type: none;
        display: inline-block;
        margin:0 10px 10px 0;
        
    }
</style>



