<template>
  <div class="PintuEdit">
      <div class="mask" v-if="!showImgManager">
          <div class="gallery">       
        <p class="title">添加拼图</p>
        <div class="close" @click="close">X</div>
        <div class="detail">
            <p class="lstp">拼图样式</p>
        </div>
        <div class="yangshi">
            <ul>
                <li @click="pinjie">
                    <img src="../assets/icon_pinjie_normal.png" alt="">
                    <p>拼接</p>
                </li>
                <li @click="dongtu">
                    <img src="../assets/icon_gif_normal.png" alt="">
                    <p>动图</p>
                </li>
            </ul>
        </div>
        <div class="ptmc">
            <p>拼图名称</p>
        </div>
        <textarea name="" id="" cols="30" rows="3" class="textarea" placeholder="输入拼图名称（建议30字以内）"></textarea>
       <div class="bottom">
           <van-button  round class="okbtn" @click="okBtn">开始拼图</van-button>
       </div>
      </div>  
      </div>
      
      <Pintuku psMsg="" @transfer="getImgManager" v-else></Pintuku>
  </div>
</template>

<script>
import Pintuku from './Pintuku'
export default {
  name: 'PintuEdit',
  data () { 
    return {
       showImgManager: false,
       imgList:[]
    }
  },
  props:['psMsg'],
  components:{Pintuku},
  methods: {
     
     okBtn() {
         
          console.log(this.imgList)
        //  var str="<img src='"+this.imgList[this.result[0]]+"' width='100%'/>"
        //  if(this.result.length==1){
        //      if(this.result[0]=='undefined'){

        //      }else{
        //          this.$emit('transfer',str)//将值绑定到transfer上传递过去
        //      }        
        //  }else{
        //       this.$toast('请选择一张图片');
        //  }
         
     },
     cancelBtn() {
        this.$emit('transfer','')
     },
    toggle(index) {
      this.$refs.checkboxes[index].toggle();
    },
    getValue(e){
        this.num = e.length
    },
    allChoose(){
        var that = this
        var map1 = that.imgList.map(function (item,index) {
            return index;
        })
        var map2 = []
        if(that.checked===false){
            that.result=map2        
        }else{
            that.result=map1    
        }      
    },
    delete_img(){
        
         var item = this.result.splice(0,this.result.length)
         for(var i=0;i<item.length;i++){
             this.imgList.splice(i,1)
         }
    },
    modifyFM(file){
        this.yl = file.content
    },
    getImgManager(msg){
        this.showImgManager = false;
        Object.assign(this.imgList,msg.splice(0,msg.length))
    },
    guanli(){
        this.showImgManager = true
    },
    close(){
       this.$emit('transfer','')
    },
    pinjie(){
        this.showImgManager = true
    },
    dongtu(){
        this.showImgManager = true
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .PintuEdit {
        position: fixed;
        left:0;
        top:0;
        width:100%;
        height:100%;
        background:#eee;
        overflow: hidden
    }
    .mask{
        width: 100%;
        height:100%;
    }
    .gallery {
        position: relative;
        margin-top:25%;
        width: 100%;
        height: 100%;
        background-color:#fff;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        overflow: auto;  
    }
    .gallery .title {
        position: fixed;
        width: 90%;
        text-align:center;
        height:50px;
        background-color:#fff;
        left:50%;
        margin:0;
        margin-left:-45%;
        line-height: 50px;
        border-bottom:solid #d6d6d6 1px;
        z-index: 20;
    }
    .gallery .close {
        position: fixed;
        right:5%;
        margin-top:3%;
        font-size:20px;
        z-index: 20;
    }
    .gallery .detail {
        display: block;
        position: relative;;
        width: 100%;
        background:#fff;
        margin-top:50px;
        z-index: 20;
    }
    .gallery .bottom {
        position:fixed;
        width: 100%;
        background:#fff;
        height:80px;
        bottom:0
    }
    .lstp {
        float:left;
        color:#000;
        padding-left:20px
    }
    .wancheng {
        float:right;
        padding-right:20px;
        color:#EF5054;
        
    }
    .wancheng img {
        width: 20px;
        vertical-align: middle;
        padding-right:5px
    }
    .wancheng span {
        vertical-align: middle
    }
    .all_choose {
        position: absolute;
        padding:30px 0 0 30px
    }
    .okbtn {
        position: absolute;
        margin:20px 0 0 30px;
        padding: 0 50px;
        background-color:#EF5054;
        color:#fff;
        font-size: 16px;
        width: 80%;
        left:50%;
        margin-left: -40%
    }
    .checkbox {
        position: absolute;
        bottom:12px;
        right:12px
    }
    .yangshi{
        position: absolute;
        width: 90%;
        top:100px;
        left:20px;
        text-align: left;
        border-bottom: solid #d6d6d6 1px;
    }
    .yangshi ul{
         position: relative;
         width: 100%;
         
    }
    .yangshi ul li{
        display: inline-block;
        width: 70px;
        text-align:center;
    }
    .yangshi ul li img{
        width: 50px;
    }
    .ptmc{
        position: absolute;
        margin-top:45%;
        padding-left:20px
    }
    .textarea{
        position: absolute;
        margin-top: 61%;
        margin-left: 15px;
        left: 0;
        background: #eee;
        width: 90%;
        padding-left:5px;
    }
</style>



