import Vue from 'vue'
import VueRouter from 'vue-router'
import ArticleList from '../components/ArticleList'
import ArticleDetail from '../components/ArticleDetail'
import edit from '../components/EditArticle'
import mycenter from '../components/myCenter'
import vip from '../components/vip'
import myarticle from '../components/myArticle'
import searchvue from '../components/search'
import myInfo from '../components/myInfo'
import MessageSet from '../components/MessageSet'
import myCustomer from '../components/myCustomer'
import EditCard from '../components/EditCard'
import ReadHistory from '../components/ReadHistory'
import modifyArticle from '../components/ModifyArticle'
import tousu from '../components/tousu'
import ModifyCompany from '../components/ModifyCompany'
import TxtEdit from '../components/TxtEdit'
import ImgEdit from '../components/ImgEdit'
import ImgManager from '../components/ImgManager'
import hasRead from '../components/hasRead'
import copyArticle from '../components/copyArticle'
import draftBox from '../components/draftBox'
import weeklyPost from '../components/weeklyPost'
import createArticle from '../components/createArticle'
import PintuEdit from '../components/PintuEdit'
import wx from 'weixin-js-sdk'
import VueCookies from 'vue-cookies'
import { Tab, Tabs, Button, List, Cell, CellGroup, Image, Field, RadioGroup, Radio, Card, Search, SwitchCell, Switch, Uploader, Popup, DatetimePicker, Toast, Dialog, Checkbox, CheckboxGroup, PullRefresh,Row,Col } from 'vant'
Vue.use(VueRouter)
// 定义路由表
const routes = [
  { path: '/', component: ArticleList, meta: { title: '首页' } },
  { path: '/detail/:id', component: ArticleDetail, meta: { title: '详情' } },
  { path: '/edit', component: edit, meta: { title: '写文章' } },
  { path: '/mycenter', component: mycenter, meta: { title: '个人中心' } },
  { path: '/vip', component: vip, meta: { title: '会员' } },
  { path: '/myArticle', component: myarticle, meta: { title: '我的文章' } },
  { path: '/search', component: searchvue, meta: { title: '搜索' } },
  { path: '/myInfo', component: myInfo, meta: { title: '个人信息' } },
  { path: '/MessageSet', component: MessageSet, meta: { title: '信息设置' } },
  { path: '/myCustomer', component: myCustomer, meta: { title: '我的客户' } },
  { path: '/EditCard', component: EditCard, meta: { title: '编辑卡片' } },
  { path: '/ReadHistory', component: ReadHistory, meta: { title: '阅读历史' } },
  { path: '/modify', component: modifyArticle, meta: { title: '修改' } },
  { path: '/tousu', component: tousu, meta: { title: '投诉' } },
  { path: '/ModifyCompany', component: ModifyCompany, meta: { title: '公司信息修改' } },
  { path: '/TxtEdit', component: TxtEdit, meta: { title: '文本编辑' } },
  { path: '/ImgEdit', component: ImgEdit, meta: { title: '图片上传' } },
  { path: '/ImgManager', component: ImgManager, meta: { title: '图库' } },
  { path: '/hasRead', component: hasRead, meta: { title: '已读文章' } },
  { path: '/draftBox', component: draftBox, meta: { title: '草稿箱' } },
  { path: '/copyArticle', component: copyArticle, meta: { title: '复制文章' } },
  { path: '/weeklyPost', component: weeklyPost, meta: { title: '客户运营周报' } },
  { path: '/createArticle', component:createArticle, meta: { title: '创建文章' } },
  { path: '/PintuEdit', component:PintuEdit, meta: { title: '拼图' } },
]

export default new VueRouter({
  routes,
  mode: 'history'
})

Vue.use(VueRouter).use(wx).use(VueCookies).use(Tab).use(Tabs).use(Button).use(List).use(Cell).use(CellGroup).use(Image).use(Field).use(RadioGroup).use(Radio).use(Card).use(Search)
Vue.use(SwitchCell).use(Switch).use(Uploader).use(Popup).use(DatetimePicker).use(Toast).use(Dialog).use(Checkbox).use(CheckboxGroup).use(PullRefresh).use(Row).use(Col)



