                        :title="item.topicName"
                        value="内容1"
                        label="描述"
                        size="large"
                        @click="enterDetail(item.topicId)"
                        clickable
                        value-class="listImg"
                        title-class="listTitle"  
                        label-class="listStar"           
                    >
                        <template slot="default">
                            <van-image
                                width="80"
                                height="80"   
                                fit="cover"                   
                                src="https://img.yzcdn.cn/vant/cat.jpeg"
                                />
                        </template>
                        <template slot="label">
                            <img src="../assets/fire.png" alt="" class="fire">
                            <img src="../assets/fire.png" alt="" class="fire">
                            <img src="../assets/fire.png" alt="" class="fire">
                            <img src="../assets/fire.png" alt="" class="fire">
                            <img src="../assets/fire.png" alt="" class="fire">
                        </template>
                    </van-cell>
                </van-list>
            <!-- </van-pull-refresh> -->
        </van-tab>
        <van-tab title="我的品牌">
            <template slot="title">
                <div class="navTitle">我的品牌</div>
            </template>
            我的品牌</van-tab>
        <van-tab title="瘦身美容">
            <template slot="title">
                <div class="navTitle">瘦身美容</div>
            </template>
            瘦身美容</van-tab>
        <van-tab title="支付">
            <template slot="title">
                <div class="navTitle">支付</div>
            </template>
            支付</van-tab>
        <van-tab title="美容">
            <template slot="title">
