        <van-button plain hairline type="danger" class="hypbtn" round>换一批</van-button>
    </div>

    <van-popup v-model="show" position="bottom"  :style="{ height: '92%' }"  >
        <div class="company">
            <h4>公司/品牌</h4>
            <van-button plain round type="danger" size="small">同仁堂</van-button>
            <van-button plain round type="danger" size="small">贝利</van-button>
            <div class="modify" @click="modify">修改></div>
        </div>
         <div class="category">
            <h4>关注的类别</h4>
            <!-- <van-button plain square  size="small" class="catbtn" @click="catbtn" >同仁堂</van-button>
            <van-button plain square  size="small" class="catbtn2" @click="catbtn">贝利</van-button> -->
                <li v-for="item in list"
                    :key="item.name"
                    :name="item">                  
                         <van-checkbox  :class="item.result?'active':'inactive'" v-model="item.result" >{{item.value}}
                            
                             <img
                                slot="icon"
                                
                            >
                             
                        </van-checkbox>                
                    </li>
                <!-- <van-checkbox
                    
                    class="checked"
                    v-model="checked"
                >
                 <img
    slot="icon"
    slot-scope="props"
    :src="props.checked ? icon.active : icon.inactive"
  >
            
                </van-checkbox> -->
        </div>   
        <van-button  size="small" type="danger" class="okbtn" @click="okbtn" >确定</van-button>
    </van-popup> 
  </div>
</template>

<script>

import Loading from '../components/Loading'
import wx from 'weixin-js-sdk'
import 'vant/lib/tabs/style'
import 'vant/lib/list/style'
import 'vant/lib/popup/style'
import 'vant/lib/image/style'
import 'vant/lib/checkbox/style'
import 'vant/lib/icon/local.css'


import { setTimeout } from 'timers';
export default {
  name: 'about',
  data () {
      return {
          checked:true,
          active: 0,
          gzlist: [],
          loading: false,
          isLoading: false,
          finished:false,
          show:false,
          pageNum:2,
          totalNum:0,
          list: [
              {"name":"0","result":true,"value":"品牌1"},
              {"name":"1","result":true,"value":"品牌2"},
              {"name":"2","result":false,"value":"品牌3"},
              {"name":"3","result":false,"value":"品牌4"},
              {"name":"4","result":true,"value":"品牌5"},
              {"name":"5","result":true,"value":"品牌6"},
              {"name":"6","result":false,"value":"品牌7"},
              {"name":"7","result":false,"value":"品牌8"},
              {"name":"8","result":false,"value":"品牌9"}
          ]
      }
  },
  components: {
      Loading
  },
  methods: {
      onClickTab(index,title){
          console.log(index,title)
      },
      enterSearch(){
          this.$router.push({path: '/search'})
      },
      onLoad() {
          setTimeout(() => {
           var that = this
           //话题列表
            that.$http.get('https://sikeqa.news.cn/qcloud/f/ask/topicList.json',{
                    params:{
                        pageNo:that.pageNum,
                        pageSize:4
                    }
                })
                .then(function(response){
                    console.log(that.pageNum,that.totalNum);
                    
                    if(that.pageNum < that.totalNum+1){
                        that.$toast('刷新成功');
                         that.gzlist = that.gzlist.concat(response.data.data.topicList)
                         that.pageNum = that.pageNum+1
                    } else{
                        that.$toast('已经是最新的了');
                    }                   
                })
                .catch(function (error) { // 请求失败处理
                    console.log(error)
                })
             }, 500);
      },
    //   onRefresh() {
    //      setTimeout(() => {
            
    //         this.isLoading = false;
    //         var that = this

    //         that.pageNum =1
    //        //话题列表
    //         that.$http.get('https://sikeqa.news.cn/qcloud/f/ask/topicList.json',{
    //                 params:{
    //                     pageNo:1,
    //                     pageSize:100
    //                 }
    //             })
    //             .then(function(response){
    //                 that.$toast('刷新成功');
    //                  that.gzlist = response.data.data.topicList             
    //             })
    //             .catch(function (error) { // 请求失败处理
    //                 console.log(error)
    //             })
    //     }, 500);
    //   },
      showPopup(){
          this.show = true
      },
      catbtn(){
          console.log(1) 
      },
      modify(){
          this.$router.push({path: '/ModifyCompany'})
      },
      okbtn(){
          console.log(this.result)
      },
      enterDetail(id){
          this.$router.push({path: 'detail/'+id})
      }

  },
  created (){
   // console.log(this.$cookies.get('uid') )
  },
  mounted () {
      var that = this
           //话题列表
            that.$http.get('https://sikeqa.news.cn/qcloud/f/ask/topicList.json',{
                    params:{
                        pageNo:1,
                        pageSize:4
                    }
                })
                .then(function(response){
                
                    that.loading = false,
                    that.gzlist = response.data.data.topicList
                    that.totalNum = response.data.data.totalPage
                    // if (that.gzlist.length  ==4) {
                    //          that.finished = true;
                    //     }   
                })
                .catch(function (error) { // 请求失败处理
                    console.log(error)
                })









      var link=window.location.href.split("#")[0]

      
    
      //微信分享
      that.$http.get('https://sikeqa.news.cn/qcloud/f/wx/getWechatParam.jspx?t='+Math.random()+'&url='+link)
       .then(data => (
           console.log(data.data.data.appid),
           wx.config({
			        debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
			        appId: data.data.data.appid, // 必填，公众号的唯一标识
			        timestamp: data.data.data.timestamp, // 必填，生成签名的时间戳
			        nonceStr: data.data.data.nonceStr, // 必填，生成签名的随机串
			        signature: data.data.data.signature,// 必填，签名，见附录1
			        jsApiList: ['checkJsApi', 'onMenuShareTimeline', 'onMenuShareAppMessage', 'onMenuShareQQ', 'onMenuShareWeibo', 'onMenuShareQZone', 'startRecord', 'stopRecord', 'onVoiceRecordEnd', 'playVoice', 'translateVoice', 'uploadVoice', 'downloadVoice'] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
			    }),

				wx.ready(function () {
			        //分享朋友圈
			        wx.onMenuShareTimeline({
			            title: 'title',
			            link: "link",
			            imgUrl: "imgSrc",// 自定义图标
			            trigger: function (res) {
			                // 不要尝试在trigger中使用ajax异步请求修改本次分享的内容，因为客户端分享操作是一个同步操作，这时候使用ajax的回包会还没有返回.
			                //alert('click shared');
			                
			            },
			            success: function (res) {
			                //alert('shared success');
			                //some thing you should do
			            },
			            cancel: function (res) {
			                //alert('shared cancle');
			            },
			            fail: function (res) {
			                //alert(JSON.stringify(res));
			            }
			        });
			        //分享给好友
			        wx.onMenuShareAppMessage({
			            title: "title", // 分享标题
			            desc: "desc", // 分享描述
			            link: "link", // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
			            imgUrl: "imgSrc", // 自定义图标
			            type: 'link', // 分享类型,music、video或link，不填默认为link
			            dataUrl: '', // 如果type是music或video，则要提供数据链接，默认为空
			            success: function () {
			                // 用户确认分享后执行的回调函数
			                // alert("success", link);
			            },
			            fail: function(){
			            	// alert("fail", link);
			            },
			            cancel: function () {
			                // 用户取消分享后执行的回调函数
			                // alert('cancel', link);
			            }
			        });
			        wx.error(function (res) {
			            // alert('error', res.errMsg);
			        })
			    })

       ))
        .catch(function (error) { // 请求失败处理
            console.log(error)
        })

        //话题列表
        // that.$http.get('https://sikeqa.news.cn/qcloud/f/ask/topicList.json',{
        //     params:{
        //         pageNo:1,
        //         pageSize:10
        //     }
        // })
        // .then(response => (
        //     console.log(response.data.data.topicList),
        //     that.posts = response.data.data.topicList    
        //     ))
        // .catch(function (error) { // 请求失败处理
        //     console.log(error)
        // })

  }
}
</script>
<style lang="css" scoped>
#TopicList {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  /* height: calc(100%-150px); */
}
.stick{
    position: fixed;
    right:0;
    top:0;
    width:20%;
    height:40px;
    background:rgba(255,255,255,0.8);
    z-index: 100;
}
.greybg{
    width:100%;
    height:8px;
    margin-top:5px;
    background: #eee
}
.search,.setting{ 
