    height:15px;
    padding-top:18px;
    display:inline-block
}
.search{
    margin-right:10px
}
.search img{
    width:100%;
    height:100%;
}
.setting img{
    width:100%;
    height:100%
}
.navTitle{
    font-size:18px;
    padding-top:5px
}
.listTitle{
    text-align: left;
    min-height:40px;
    font-size:18px;
    line-height: 1.5;
    width:82%;
}
.listImg{
    width: 80px;
    height: 80px;
    border-radius: 6px;
    overflow: hidden;
}
.van-cell__title, .van-cell__value {
    flex:auto
}
.fire{
    width:10px;
}
.overlay1{
    margin-top:8%;
    background:red
}
.company,.category{
    text-align: left;
    padding:0 5%;
}
.company{
    margin-bottom:20px
}
.company h4{
    padding:10px 0 10px 0;
    margin:0;
    display: block
}
.category h4{
    padding:10px 0 10px 0;
    margin:0;
    display: block
}
.company .modify{
    position:absolute;
    right:10px;
    top:10px;
    font-size:12px;
    color:#c9c9c9
}
.category li{
    list-style-type: none;
    display: inline-block;
    min-width:60px;
    margin-right:15px;
    margin-bottom:10px;
}
.category .inactive{
    border:none;
    background-color:#f4f5f7;
    padding:5px 5px 5px 0;
    border-radius: 8px
}
.category .active{
    border:none;
    background:url(../assets/btnbg.png) no-repeat;
    background-size:cover;
    width:62px;
    height:27px
}
.bottomTip{
    position: fixed;
    bottom:0;
    width:100%;
    background:#fff;
    padding:12px 0
}
.bottomTip .txt{
    display: inline;
}
.bottomTip .hypbtn{
    width:70px;
    height:26px;
    padding:0;
    line-height: 1.5;
    overflow: hidden;
}
.okbtn{
    width:92%;
    position: absolute;
    bottom:50px;
    left:50%;
    margin-left:-46%;
}
.icheckbox{
    height:50px;
    width:50px;
}
.icheckbox img{
    height:30px;
    width:30px;
}
</style>



