<template>
  <div class="weeklyPost">
      <div class="header">
        <div class="customInfo">
            <img src="https://img.yzcdn.cn/vant/cat.jpeg" alt="" class="avator">
            <p class="nickname">客户昵称</p>
        </div>
        <img src="../assets/weeklybg.png" alt="" class="weeklybg">
        <div class="titleInfo">
            <h1>上周概况</h1>
            <p>2019.07.08 - 2019.07.14</p>
        </div>
        <div class="weeklyDetail">
            <ul>
                <li>原创文章</li>
                <li>文章查看量</li>
                <li>文章转发量</li>
            </ul>
            <ul>
                <li><span>5</span>篇</li>
                <li><span>5000</span>次</li>
                <li><span>2325</span>次</li>
            </ul>
            <div class="totalNum">
                <p class="p1">转发量超过</p>
                <p class="p2">60%</p>
                <p class="p3">的作者</p>
                <p class="p4">07.08 - 07.14</p>
            </div>
            <div class="result">
                <p class="t1">本周文章转发量已超过60%的作者</p>
                <p class="t2">继续加油！</p>
            </div>
        </div>
    </div>
    <div class="list">
        <div class="title">最受欢迎文章</div>
         <van-list
                v-model="loading"
                :finished="finished"
                finished-text="没有更多了"
                @load="onLoad"
                >
                <div class="greybg"></div>
                    <van-cell
                        v-for="item in gzlist"
                        :key="item.topicId"
                        :title="item.topicName"
                        value="内容1"
                        label="描述"
                        size="large"
                        @click="enterDetail(item.topicId)"
                        clickable
                        value-class="listImg"
                        title-class="listTitle"  
                        label-class="listStar"           
                    >
                        <template slot="default">
                            <van-image
                                width="80"
                                height="80"   
                                fit="cover"                   
                                src="https://img.yzcdn.cn/vant/cat.jpeg"
                                />
                        </template>
                        <template slot="label">
                            <img src="../assets/icon_redu.png" alt="" class="fire">
                            <img src="../assets/icon_redu.png" alt="" class="fire">
                            <img src="../assets/icon_redu.png" alt="" class="fire">
                            <img src="../assets/icon_redu.png" alt="" class="fire">
                            <img src="../assets/icon_redu.png" alt="" class="fire">
                        </template>
                    </van-cell>
                </van-list>
    </div>
  </div>
</template>

<script>
export default {
  name: 'weeklyPost',
  data () {
    return {
        gzlist:[],
         loading: false,
          isLoading: false,
          finished:false,
          show:false,
          pageNum:2,
          totalNum:0,
    }
  },
  methods: {
       onLoad() {
          setTimeout(() => {
           var that = this
           //话题列表
            that.$http.get('https://sikeqa.news.cn/qcloud/f/ask/topicList.json',{
                    params:{
                        pageNo:that.pageNum,
                        pageSize:4
                    }
                })
                .then(function(response){
                    console.log(that.pageNum,that.totalNum);
                    
                    if(that.pageNum < that.totalNum+1){
                        that.$toast('刷新成功');
                         that.gzlist = that.gzlist.concat(response.data.data.topicList)
                         that.pageNum = that.pageNum+1
                    } else{
                        that.$toast('已经是最新的了');
                    }                   
                })
                .catch(function (error) { // 请求失败处理
                    console.log(error)
                })
             }, 500);
      },
  },
  mounted () {
      var that = this
           //话题列表
            that.$http.get('https://sikeqa.news.cn/qcloud/f/ask/topicList.json',{
                    params:{
                        pageNo:1,
                        pageSize:4
                    }
                })
                .then(function(response){
                
                    that.loading = false,
                    that.gzlist = response.data.data.topicList
                    that.totalNum = response.data.data.totalPage
                    // if (that.gzlist.length  ==4) {
                    //          that.finished = true;
                    //     }   
                })
                .catch(function (error) { // 请求失败处理
                    console.log(error)
                })
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .weeklyPost{
        position: absolute;
        left:0;
        top:0;
        width:100%;
        height:100%;
        background:#fff;
    }
    .header {
        position: relative;
        width: 100%;
        background-color:#ed464a;
        padding-bottom:1px
    }
    .customInfo {
        position: relative;
        text-align: left;
        padding:20px
    }
    .customInfo .avator {
        display: inline-block;
        width: 60px;
        height: 60px;
        border-radius: 80px;
        vertical-align: middle
      
    }
    .customInfo .nickname {
        display: inline-block;
        color:#fff;
        vertical-align: middle;
        font-size:18px;
        padding-left:10px
    }
    .weeklybg {
        position: absolute;
        width:100%;
        top:20px;
        right:0
    }
    .titleInfo{
        position: relative;
        text-align: left;
        color:#fff;
        padding-left:20px
    }
    .titleInfo h1{
        margin:0
    }
    .titleInfo p{
        margin: 0
    }
    .weeklyDetail {
        position: relative;
        background-color:#fff;
        width: 90%;
        margin:40px auto;
        border-radius: 20px;
        padding-top:20px;
    }
    .weeklyDetail ul{
        position: relative;
        width: 100%;
        display: flex;
        justify-content:space-evenly;
        padding:5px
    }
    .weeklyDetail ul li{
        text-align:center
    }
    .weeklyDetail ul li span{
        font-size:28px;
        padding-right:5px
    }
    .weeklyDetail .totalNum{
        width: 120px;
        height:127px;
        margin:0 auto;
        background-image: url(../assets/icon_xunzhang.png);
        background-repeat: no-repeat;
        background-size: cover;
    }
    .weeklyDetail .totalNum p{
        line-height: 1.2;
    }
    .weeklyDetail .totalNum .p1{
        padding-top:22px;
        color:#906E00;
        margin:0
    }
    .weeklyDetail .totalNum .p2{
        color:#6E5400;
        font-size:30px;
        margin:0;
        font-weight: 700
    }
    .weeklyDetail .totalNum .p3{
        color:#906E00;
        margin:0
    }
    .weeklyDetail .totalNum .p4{
        color:#fff;
        width: 160px;
        height:20px;
        margin:0 auto;
        margin-left:-18px;
        line-height:1.2;
        background-image: url(../assets/icon_riqilable.png);
        background-repeat: no-repeat;
        background-size: cover
    }
    .result .t1{
        color:#666;
    }
    .result .t2{
        font-size:24px;
        margin:0;
        padding-bottom:20px
    }
    .list {
        position: relative;
    }
    .list .title{
        text-align: left;
        margin-left:20px;
        padding-left:10px;
        margin-top:20px;
        border-left:4px solid #EF5054
    }
    .listTitle{
    text-align: left;
    min-height:40px;
    font-size:18px;
    line-height: 1.5;
    width:82%;
    }
    .listImg{
        width: 80px;
        height: 80px;
        border-radius: 6px;
        overflow: hidden;
    }
    .van-cell__title, .van-cell__value {
        flex:auto
    }
    .fire{
        width:15px;
    }
</style>



