<template>
    <div id="myCenter">
        <div class="notice">
           
                微名片自动添加到您的文章地步，完善修改名片宣传自己
           
        </div>
        <div class="card">
            <img src="../assets/avatar.png" alt="" class="avatarCard">
            <div class="nameCard">足球部落</div>
            <div class="btn">
                <van-button plain type="danger" round icon="https://linkme.oss-cn-beijing.aliyuncs.com/share/i/tel.png" size="small" @click="telto" style="margin-right:10px;padding:0 16px">打电话</van-button>
                <van-button plain type="danger" round icon="https://linkme.oss-cn-beijing.aliyuncs.com/share/i/weixin.png" size="small" @click="weixin" style="margin-right:10px;padding:0 16px">加微信</van-button>
                <van-button plain type="danger" round icon="https://linkme.oss-cn-beijing.aliyuncs.com/share/i/home.png" size="small" @click="backhome" style="padding:0 16px">微主页</van-button>
            </div>
            <div class="brief">
                <h3>个人简介</h3>
                <p>具有良好的职业操守和集体荣誉意识，踏实肯干，虚心好学，有一定的抗压能力，能较快的适应环境并不断总结经验。</p>
            </div>
            <div class="changeBtn" @click="jumptochange">修改名片</div>
        </div>

        <div class="mylist">
            <van-cell title="开通会员专属特权" is-link icon="https://linkme.oss-cn-beijing.aliyuncs.com/share/i/hydq.png" value="立即开通" title-class="mylisttitle"	url to="vip" />
            <van-cell title="已创建文章" is-link icon="https://linkme.oss-cn-beijing.aliyuncs.com/share/i/ycj.png" value="1" title-class="mylisttitle" url to="myArticle" />
        </div>

        <van-popup v-model="show">
            <img src="../assets/avatar.png" alt="">
        </van-popup>
    </div>
</template>

<script>
import { Dialog } from 'vant'
export default {
    data () {
      return {
          show: false
          
      }
  },
  methods: {
      jumptochange(){
          this.$router.push({path: '/EditCard'})
      },
      telto(){
          window.location.href='tel://18911201110'
      },
      weixin(){
          this.show = true
      },
      backhome(){
          this.$router.push({path: '/'})
      }
  }
}
</script>


<style scoped>
    #myCenter{
        position: absolute;
        top:0;
        left:0;
        width:100%;
        height:100%;
        background:#f2f2f2;
    }
    .notice{
        margin-bottom:50px;
        font-size:12px;
        padding:5px 0;
        color:#9ab3c9;
        background:#e7edf3
    }
    .card{
        position: relative;
        width:92%;
        margin:0 4%;
        height:260px;
        background:#fff;
        border-radius:10px;
        margin-bottom:14px;
    }
    .avatarCard{
        width:70px;
        margin-top:-35px;
        padding:3px;
        background:#fff;
        border-radius: 100%;
        margin-bottom:10px;
    }
    .nameCard{
        margin-bottom:12px
    }
    .brief h3{
        text-align: left;
        border-left: #ef5054 solid 3px;
        padding-left:10px;
        margin-left:10px;
        font-size:16px
    }
    .brief p{
        text-align: left;
        padding:0 20px;
        font-size:14px;
        line-height: 1.5;
        color:#999
    }
    .changeBtn{
        position: absolute;
        padding:4px 14px;
        background:#e3e3e3;
        color:#666666;
        font-size:14px;
        top:20px;
        right:0;
        border-radius: 20px 0 0 20px
    }
    .mylisttitle{
        text-align: left;
        font-size:16px;
    }
     .van-icon__image{
        width:15px
    }
</style>




