<template>
  <div id="searchvue">
    <form action="/">
        <van-search
            v-model="value"
            placeholder="请输入搜索关键词"
            show-action
            @search="onSearch"
            @cancel="onCancel"
        />
    </form>
  </div>
</template>

<script>
import 'vant/lib/search/style'
export default {
  name: 'searchvue',
  data () {
    return {
      value:''
    }
  },
  methods:{
      onSearch(){
          console.log('search')
      },
      onCancel(){
          this.$router.push({path: '/'})
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    #searchvue{

    }
</style>



