<template>
  <div class="vip">
      <div class="des">
          <h3>会员特权</h3>
          <van-cell value="查看你文章的人" icon="https://linkme.oss-cn-beijing.aliyuncs.com/share/i/eye.png" />
          <van-cell value="不限制推送文章的类型/条数" icon="https://linkme.oss-cn-beijing.aliyuncs.com/share/i/tui.png" />
          <van-cell value="可修改文章、创建文章" icon="https://linkme.oss-cn-beijing.aliyuncs.com/share/i/pen.png" />
      </div>
      <!-- <van-radio name="1" checked-color="red"/> -->
      <div class="radio">
          <van-radio-group v-model="radio" @change="pricechoose"> 
            <van-cell-group>
                <van-cell title="3个月39元" title-class="listTitle"  clickable @click="radio = '39'">
                <template slot="title">
                    <div class="priceTitle">3个月<span>39</span>元</div>
                </template>
                  <van-radio name="39" checked-color="red" />
                </van-cell>
                <van-cell title="半年69元"  title-class="listTitle"  clickable @click="radio = '69'">
                <template slot="title">
                    <div class="priceTitle">半年<span>69</span>元</div>
                </template>
                  <van-radio name="69" checked-color="red" />
                </van-cell>
                <van-cell title="1年99元"  title-class="listTitle"  clickable @click="radio = '99'">
                  <template slot="title">
                    <div class="priceTitle">1年<span>99</span>元</div>
                  </template>
                  <van-radio name="99" checked-color="red" />
                </van-cell>
            </van-cell-group>
        </van-radio-group>
      </div>
      <div>
          <van-button round type="danger" class="pay">微信支付{{ price }}元</van-button>
      </div>
  </div>
</template>

<script>
import 'vant/lib/radio/style'

export default {
  name: 'vip',
  data () {
    return {
        radio: '1',
        price: 99
    }
  },
  methods: {
      pricechoose(e){
          this.price=e
      },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .vip{
        position: absolute;
        left:0;
        top:0;
        width:100%;
        height:100%;
        background:#f2f2f2;
    }
    h3{
        text-align: left;
        padding-left:20px;
        font-size:16px;
        margin-top:0px;
        padding-top:20px;
        margin-bottom:10px;
    }
    .des .van-cell{
        border:none;
        background:none;
        padding:5px 15px;
    }
    .radio{
        margin-top:10px;
        padding:10px 14px;
        margin-bottom:300px
    }
    .listTitle{
        text-align: left
    }
    .radio .van-cell-group{
        background:none
    }
    .radio .van-cell{ 
        margin-bottom:10px;
        padding:20px 16px;
        font-size:14px;
        border-radius: 14px;
        box-shadow: 0 0 10px  #d9dadc;
        flex:auto;
        outline: #d41525
    }
    .radio .van-cell__value{
        position: absolute;
        right:15px
    }
    .priceTitle span{
        color:red;
        font-size:18px;
        font-weight: 500;
    }
    .pay{
        position:absolute;
        bottom:20px;
        width:90%;
        left:50%;
        margin-left:-45%
    }
</style>



