// 导入vue实例
import Vue from 'vue'
// 导入 App 组件
import App from './App'

import router from './router'
import axios from 'axios'
import d3 from 'd3'
// 导入 vue router

Vue.config.productionTip = false
Vue.prototype.$http = axios
// Vue.use(d3)
router.beforeEach((to, from, next) => {
  if (to.meta.title) {
    document.title = to.meta.title
  }
  next()
})

// 实例化Vue实例
new Vue({
  // 定义Vue绑定的跟元素
  el: '#app',
  // 用<App/>代替根元素
  template: '<App/>',
  // 声明App组件，这样上面的<App/>元素就可以生效
  components: { App },
  // 将上面声明的路由器传递到根Vue实例
  router
}).$mount('#app')



