<template>
    <div id="edit">
       <form role="form">
           <div class="form-group">
                <p class="label">文章标题</p>
                <van-cell-group>
                    <van-field
                        v-model="title"
                        type="textarea"
                        placeholder="请输入文章标题（建议30字以内）"
                        rows="3"
                        autosize
                    >
                    <van-button slot="button" class="outerBtn">
                        <van-uploader :after-read="modifyFM"  class="uploadBtn">
                            <van-image
                                width="90"
                                height="90"   
                                fit="cover"                   
                                :src="fm"
                            />
                        </van-uploader>
                    </van-button>
                    </van-field>
                </van-cell-group>
           </div>
           <div class="form-group">
                <p class="label">文章摘要</p>
                <van-cell-group>
                    <van-field
                        v-model="content"
                        type="textarea"
                        placeholder="请输入文章摘要（建议50字以内，可不填写）"
                        rows="4"
                        autosize
                    >
                    </van-field>
                </van-cell-group>
           </div>
            <div class="form-group new-content" v-for="(item,i) in myData" :key="i">
                <div class="line"></div>
                 <van-cell-group class="outer">
                    <van-field
                        v-model="item.content"
                        type="textarea"
                        placeholder=""
                        rows="2"
                        autosize
                        class="newTxtInput"
                        v-if="inputShow"
                    >
                    </van-field>
                    <div class="newTxt" v-html="item.content" v-else></div>
                </van-cell-group>
                <ul class="content_btn">
                    <li @click="deleteItem(i)"><img src="../assets/icon_delete.png" alt=""><span>删除</span></li>
                    <li @click="moveUpItem(i)"><img src="../assets/icon_shangyi.png" alt=""><span>上移</span></li>
                    <li @click="moveDownItem(i)"><img src="../assets/icon_add.png" alt=""><span>下移</span></li>
                    <li @click="editItem(i)"><img src="../assets/icon_nrbianji.png" alt=""><span>编辑</span></li>
                </ul>
            </div>



        <div class="moreMask" v-if="moreMaskShow">
            <ul>
                <li @click="addTxt">
                    <img src="../assets/icon_wenzi.png" alt="">
                    <p>文字</p>
                </li>
                <li @click="addImg">
                    <img src="../assets/icon_tupian.png" alt="">
                    <p>图片</p>
                </li>
                <li @click="addPin">
                    <img src="../assets/icon_pintu.png" alt="">
                    <p>拼图</p>
                </li>
            </ul>
        </div>
        <div class="addmore" v-if="addMoreShow">
            <img src="../assets/icon_tjnr.png" alt="" class="addBtn" @click="moreMask">
        </div>
        <div class="bottomBtn">
            <van-button  type="danger" square  size="large" class="cancel" @click="cancelModify">存到草稿箱</van-button>
            <van-button  type="danger"  square size="large" class="save" @click="saveModify">发布</van-button>
        </div>
       </form>
       <txtEdit psMsg="" @transfer="getTxt" v-if="showTxt"></txtEdit>
       <imgEdit psMsg="" @transfer="getImg" v-if="showImg"></imgEdit>
    </div>
</template>

<script>
import txtEdit from './TxtEdit'
import ImgEdit from './ImgEdit'
export default {
    data(){
        return{
            title:'',
            content:'',
            fileList: [],
            fm: 'https://linkme.oss-cn-beijing.aliyuncs.com/share/i/uploadFM.jpg',
            moreMaskShow: false,
            myData:[],
            showTxt:false,
            showImg:false,
            addMoreShow:true,
            inputShow:false
        }
    },
    components:{txtEdit,ImgEdit},
    methods: {
    modifyFM(file){
        this.fm = file.content
    },
    moreMask() {
        this.moreMaskShow = !this.moreMaskShow
    },
     cancelModify() {
        //this.moreMask = true
    },
     saveModify() {
        //this.moreMask = true
    },
    addTxt() {
        this.showTxt = true,
        this.addMoreShow = false,
        this.moreMaskShow = false
    },
    addImg() {
        this.showImg = true,
        this.addMoreShow = false,
        this.moreMaskShow = false
    },
    addPin() {

    },
    getTxt(msg){
        this.showTxt = false,
        this.addMoreShow = true,
        this.moreMaskShow = false
        if(msg!=''){
             this.myData.push({
                content:msg
            })
        } 
    },
    getImg(msg){
        this.showImg = false,
        this.addMoreShow = true,
        this.moreMaskShow = false
        if(msg!=''){
             this.myData.push({
                content:msg
            })
        } 
    },
    deleteItem(e){
         this.myData.splice(e,1)
    },
    moveUpItem(e){
        this.myData.splice(e-1,0,this.myData[e])
        this.myData.splice(e+1,1)
    },
    moveDownItem(e){
        this.myData.splice(e+2,0,this.myData[e])
        this.myData.splice(e,1)
    },
    editItem(e){
        console.log(this.myData[e].content)
    }
  }
}
</script>

<style scoped>
#edit {
    position: absolute;
    left:0;
    top:0;
    width:100%;
    height:100%;
    background:#f2f2f2
}
.label {
    text-align: left;
    padding-left: 10px
}
.outerBtn {
    border:none;
    width: 120px;
}
.bottomBtn{
    position: fixed;
    bottom:0px;
    left:0;
    width:100%;
    display: flex
}

.bottomBtn .cancel {
    width:100%;
    padding:0;
    border:none;
    background:#d9d9d9;
    color:#000;
    font-size:18px;
}
.bottomBtn .save {
    width:100%;
    padding:0;
    border:none;
    font-size:18px;
}
.addmore {
    position:fixed;
    bottom:70px;
    width: 100%;
    z-index: 11;
}
.addBtn {
    width: 60px;
}
.moreMask {
    background: #fff;
    width: 100%;
    height:105px;
    position:fixed;
    bottom:0;
    z-index: 10;
}
.moreMask ul {
    display: flex;
    justify-content: space-between;
    width: 60%;
    margin: 0 auto
}
.moreMask ul li {
    margin-top: 16%;
    display: inline-block;
}
.moreMask ul li img{
    width: 40px;
}
.moreMask ul li p{
    margin:0
}
.line {
    width: 100%;
    height: 20px;
    background-color: #f0f0f0
}
form {
    margin-bottom:150px;
}
.content_btn {
    width: 100%;
    background-color: #fff;
    vertical-align: middle;
    padding-bottom:10px;
    padding-top:10px;
}
.content_btn li {
    display: inline-block;
    padding-right: 10px;
    border-right: #d6d6d6 solid 1px
}
.content_btn li:last-child {
    border:none
}
.content_btn li img {
    padding-right:10px;
    width: 20px;
    vertical-align: middle
}
.content_btn li span {
    color: #EF5054;
    vertical-align: middle
}
.new-content {
    width: 100%;
}
.newTxt {
    width: 94%;
    margin:0 auto;
    text-align: left;
    background-color: #f5f5f5;
    border: 1px solid #d6d6d6;
}
.newTxt img{
    width: 100%;
}
.outer {
    padding-top:20px
}
</style>



