<template>
    <div id="ReadHistory">
        <div class="head">
            <h3>习近平向2019中国国际大数据产业博览会致贺信</h3>
            <p class="datetime">分享时间：2019-05-05</p>
            <div class="num">
                <div class="znum">阅读总人数：<span>390</span></div>
                <div class="znum">文章分享次数：<span>908</span></div>
            </div>
        </div>
        <div class="relationship">
            <div class="des">查看阅读关系图</div>
            <div class="relation">
                <div class="tree-svg" id="treeId"></div>
            </div>

        </div>
    </div>
</template>

<script>
import d3 from 'd3'
var diagonal = d3.svg.diagonal()
    .projection(function(d){ return [d.x,d.y] })
export default {
    name: 'ReadHistory',
    data(){
        return{
            root: {
            "name": "flare",
            "size": "1",
            "image": "https://img.yzcdn.cn/vant/cat.jpeg",
            "children": [{
            "name": "AAA",
            "size": "11",
            "image": "https://img.yzcdn.cn/vant/cat.jpeg"
            },{
            "name": "BBB",
            "size": "12",
            "image": "https://img.yzcdn.cn/vant/cat.jpeg"
            }]
        },
        tree: null,
        zm: null,
        height: 750,
        count: 0,
        duration: 800,

        }
    },
    created() {
        var that = this
        var tree = d3.layout.tree().nodeSize([60,60]);
        that.tree = tree
    },
    mounted () {
    var that = this;
    var svg = d3.select("#treeId").append("svg").attr("width", 750).attr("height", 1130)
          .call(that.zm = d3.behavior.zoom().scaleExtent([1,3]).on("zoom", d=>{svg.attr("transform", "translate(" + d3.event.translate + ")" );}))
          .append("g")
          .attr("transform", "translate(" + 200 + "," + 50 + ")");
 
        that.zm.translate([150, 50]);
        that.svg = svg
        that.root.x0 = 0;
        that.root.y0 = that.height / 2;
 
        function collapse(d) {
          console.log(d)
          if (d.children) {
            d._children = d.children;
            d._children.forEach(collapse);
            d.children = null;
          }
        }
        // Initialize the display to show a few nodes.
        that.root.children.forEach(collapse);
        
        that.update(that.root);
  },

  methods: {
    update (source) {
      var that = this
      // Compute the new tree layout.
      var nodes = that.tree.nodes(that.root).reverse(),
      links = that.tree.links(nodes);
 
      // Normalize for fixed-depth.
      nodes.forEach(function(d) { d.y = d.depth * 180; });
       // Update the nodes…
      var node = that.svg.selectAll("g.node")
        .data(nodes, function(d) { return d.id || (d.id = ++that.count); });
 
      // Enter any new nodes at the parent's previous position.
      var nodeEnter = node.enter().append("g")
        .attr("class", "node")
        .attr("transform", function(d) {
          return "translate(" + source.x0 + "," + source.y0 + ")"; 
        })
        .on("click", d => that.click(d));
 
      nodeEnter.append('image')
        .attr('xlink:href', d => {
            return d.image
        })
        .attr('x', d => {
          return d.children || d._children ? -25 : -25
        })
        .attr('y', -50)
 
      nodeEnter.append("text")
      .attr("x", function(d) { return d.children || d._children ? -20 : -20; })
      .attr("y", "15")
      .attr("font-size", "18px")
      .text(function(d) { return d.name; })
      .style("fill-opacity", 1e-6);
 
      // Transition nodes to their new position.
      var nodeUpdate = node.transition()
        .duration(that.duration)
        .attr("transform", function(d) { return "translate(" + d.x + "," + d.y + ")"; });
 
      nodeUpdate.selectAll('image')
        .attr('width', 50)
        .attr('height', 50)
 
      nodeUpdate.select("text")
        .style("fill-opacity", 1);
 
      // Transition exiting nodes to the parent's new position.
      var nodeExit = node.exit().transition()
        .duration(that.duration)
        .attr("transform", function(d) { return "translate(" + source.x + "," + source.y + ")"; })
        .remove();
 
      nodeExit.select('image')
        .attr('width', 0)
        .attr('height', 0)
        
      nodeExit.select("text")
        .style("fill-opacity", 1e-6);
 
      // Update the links…
      var link = that.svg.selectAll("path.link")
        .data(links, function(d) {
          return d.target.id;
        });
 
      // Enter any new links at the parent's previous position.
       link.enter().insert("svg:path", "g")
        .attr("class", "link")
        .attr("fill", "none")
        .attr("stroke", "#ccc")
        .attr("stroke-width", "2")
        .attr("d", function(d) {
          var o = {x: source.x0, y: source.y0};
          return diagonal({source: o, target: o});
        });
 
      // Transition links to their new position.
      link.transition()
        .duration(that.duration)
        .attr("d", diagonal);
      
      // Transition exiting nodes to the parent's new position.
      link.exit().transition()
        .duration(that.duration)
        .attr("d", function(d) {
          var o = {x: source.x, y: source.y};
          return diagonal({source: o, target: o});
        })
        .remove();
      
      // Stash the old positions for transition.
      nodes.forEach(function(d) {
        d.x0 = d.x;
        d.y0 = d.y;
      });
    },
    click(d) {
      console.log(d)
      var that = this
      // #重点关注这个函数的不同之处。尤其是else部分
      
      if (d.children) {
        d._children = d.children;
        d.children = null;
      } else if(d._children){     
        d.children = d._children;
        d._children = null;
      }else {
        var mnodes = that.getNode(d.size);
        d.children = mnodes;        
      }
      that.update(d);
    },
    getNode (id) { 
      // #自定义的一个新的以同步方式从后台取数据的ajax函数
      var mynodes = [];
      if (id === '11') {
        mynodes = [{
          "name": "AAA01",
          "image": "",
          "size": "111"  
        },{
          "name": "AAA02",
          "image": "https://img.yzcdn.cn/vant/cat.jpeg",
          "size": "112"  
        },{
          "name": "AAA03",
          "image": "https://img.yzcdn.cn/vant/cat.jpeg",
          "size": "113"
        }];
      } else if(id === '12') {
        mynodes = [{
          "name": "BBB01",
          "image": "https://img.yzcdn.cn/vant/cat.jpeg",
          "size": "121"  
        },{
          "name": "BBB02",
          "image": "https://img.yzcdn.cn/vant/cat.jpeg",
          "size": "122"  
        },{
          "name": "BBB03",
          "image": "https://img.yzcdn.cn/vant/cat.jpeg",
          "size": "123"
        }];
      } else {
        mynodes = [{
          "name": "DDD",
          "image": "https://img.yzcdn.cn/vant/cat.jpeg",
          "size": "131"  
        },{
          "name": "EEE",
          "image": "https://img.yzcdn.cn/vant/cat.jpeg",
          "size": "132"  
        },{
          "name": "FFF",
          "image": "https://img.yzcdn.cn/vant/cat.jpeg",
          "size": "133"
        }];
      } 
      return mynodes;
    },
}


}
</script>

<style lang="css">
* { touch-action: pan-y; } 
#ReadHistory{
    position: absolute;
    left:0;
    top:0;
    width:100%;
    height:100%;
    background:#f3f3f3; 
    overflow: hidden;   
         
}
.head h3{
    text-align: left;
    padding:0 20px
}
.head .datetime{
    text-align: left;
    padding:0 20px;
    color:#999;
    font-size:12px
}
.num{
    display: flex;
    width:100%;
    border-top:1px solid #c8c5c5;
    padding:15px 0
}
.num .znum{
    text-align: center;
    width:50%;
    font-size:14px
}
.num .znum:first-child{
    border-right: 1px solid #c8c5c5;
}
.num .znum span{
    color:#ef5054
}
.relationship{
 
    width:100%;
    height:100%;
    background:#fff;
}
.relationship .des{
    text-align: left;
    color:#999;
    font-size:12px;
    padding:10px 10px
}
.tree-svg{
    margin:0 auto;
}
</style>



