<template>
  <div class="ImgEdit">
      <div class="mask" v-if="!showImgManager">
          <div class="gallery">       
        <p class="title">添加图片（每次只能添加1张）</p>
        <div class="close" @click="close">X</div>
        <div class="detail">
            <p class="lstp">历史图片</p>
            <p class="wancheng" @click="guanli"><img src="../assets/icon_guanli.png"/><span></span>管理</p>
        </div>
        <ul>
          <!-- <li  v-for="item in imgList" :key="item">
              <img :src="item" alt="">
              <van-checkbox v-model="checked" class="choose" checked-color="#EF5054"></van-checkbox>
          </li> -->
          
          <van-checkbox-group v-model="result" :max="1" @change="getValue" class="van-checkbox-group">
            <van-cell-group class="van-cell-group">
                <van-cell class="li">
                    <van-uploader :after-read="modifyFM"  class="uploadBtn">
                        <van-image width="100" height="100" fit="cover" :src="yl" />
                    </van-uploader>
                    <van-checkbox
                    ref="checkboxes"
                    checked-color="#EF5054"
                    slot="right-icon"
                    class="checkbox"
                    />
                </van-cell>
                <van-cell
                v-for="(item, index) in imgList"
                clickable
                :key="item"
                :name="item"
                @click="toggle(index)"
                class="li"
                >
                    <img :src="item" alt="">
                    <van-checkbox
                    :name="index"
                    ref="checkboxes"
                    checked-color="#EF5054"
                    slot="right-icon"
                    class="checkbox"
                />
             
                
                </van-cell>
            </van-cell-group>
         </van-checkbox-group>
       </ul>
       <div class="bottom">
           <van-button  round class="okbtn" @click="okBtn">确定</van-button>
       </div>
      </div>  
      </div>
      
      <ImgManager psMsg="" @transfer="getImgManager" v-else></ImgManager>
  </div>
</template>

<script>
import ImgManager from './ImgManager'
export default {
  name: 'TxtEdit',
  data () { 
    return {
       imgList:[
           'https://linkme.oss-cn-beijing.aliyuncs.com/share/i/1.png',
           'https://linkme.oss-cn-beijing.aliyuncs.com/share/i/2.png',
           'https://linkme.oss-cn-beijing.aliyuncs.com/share/i/3.png',
           'https://linkme.oss-cn-beijing.aliyuncs.com/share/i/4.png',
           'https://linkme.oss-cn-beijing.aliyuncs.com/share/i/5.png',
           'https://linkme.oss-cn-beijing.aliyuncs.com/share/i/6.png',
           'https://linkme.oss-cn-beijing.aliyuncs.com/share/i/7.png',
           'https://linkme.oss-cn-beijing.aliyuncs.com/share/i/8.png',
           'https://linkme.oss-cn-beijing.aliyuncs.com/share/i/9.png',
           'https://linkme.oss-cn-beijing.aliyuncs.com/share/i/10.png',
           'https://linkme.oss-cn-beijing.aliyuncs.com/share/i/9.png',
           'https://linkme.oss-cn-beijing.aliyuncs.com/share/i/10.png',
           'https://linkme.oss-cn-beijing.aliyuncs.com/share/i/9.png',
           'https://linkme.oss-cn-beijing.aliyuncs.com/share/i/10.png',
      
       ],
       result: [],
       checked: false,
       num:0,
       yl:'https://linkme.oss-cn-beijing.aliyuncs.com/share/i/upload.png',
       showImgManager: false
    }
  },
  props:['psMsg'],
  components:{ImgManager},
  methods: {
     
     okBtn() {
         console.log(this.imgList[this.result[0]])
         var str="<img src='"+this.imgList[this.result[0]]+"' width='100%'/>"
         if(this.result.length==1){
             if(this.result[0]=='undefined'){

             }else{
                 this.$emit('transfer',str)//将值绑定到transfer上传递过去
             }        
         }else{
              this.$toast('请选择一张图片');
         }
         
     },
     cancelBtn() {
        this.$emit('transfer','')
     },
    toggle(index) {
      this.$refs.checkboxes[index].toggle();
    },
    getValue(e){
        this.num = e.length
    },
    allChoose(){
        var that = this
        var map1 = that.imgList.map(function (item,index) {
            return index;
        })
        var map2 = []
        if(that.checked===false){
            that.result=map2        
        }else{
            that.result=map1    
        }      
    },
    delete_img(){
        
         var item = this.result.splice(0,this.result.length)
         for(var i=0;i<item.length;i++){
             this.imgList.splice(i,1)
         }
    },
    modifyFM(file){
        this.yl = file.content
    },
    getImgManager(msg){
        this.showImgManager = false
    },
    guanli(){
        this.showImgManager = true
    },
    close(){
       this.$emit('transfer','')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .ImgEdit {
        position: fixed;
        left:0;
        top:0;
        width:100%;
        height:100%;
        background:#eee;
        overflow: hidden
    }
    .mask{
        width: 100%;
        height:100%;
    }
    .gallery {
        position: relative;
        margin-top:25%;
        width: 100%;
        height: 100%;
        background-color:#fff;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        overflow: auto;  
    }
    .gallery .title {
        position: fixed;
        width: 90%;
        text-align:center;
        height:50px;
        background-color:#fff;
        left:50%;
        margin:0;
        margin-left:-45%;
        line-height: 50px;
        border-bottom:solid #d6d6d6 1px;
        z-index: 20;
    }
    .gallery .close {
        position: fixed;
        right:5%;
        margin-top:3%;
        font-size:20px;
        z-index: 20;
    }
    .gallery .detail {
        position:fixed;
        width: 100%;
        background:#fff;
        margin-top:50px;
        z-index: 20;
    }
    .gallery .bottom {
        position:fixed;
        width: 100%;
        background:#fff;
        height:80px;
        bottom:0
    }
    .lstp {
        float:left;
        color:#999;
        padding-left:20px
    }
    .wancheng {
        float:right;
        padding-right:20px;
        color:#EF5054;
        
    }
    .wancheng img {
        width: 20px;
        vertical-align: middle;
        padding-right:5px
    }
    .wancheng span {
        vertical-align: middle
    }
    ul {
        width: 100%;
        height:100%;
        overflow: scroll;
        text-align: center;
        margin:100px auto 180px auto;
        z-index: 10;
    }
    ul .van-checkbox-group{
        width:100%;
        height:100%;
        overflow: scroll;
    }
    ul .van-cell-group{
        width:100%;
        height:100%;
        overflow: scroll;
    }
    ul .li {
        position: relative;
        display:inline-block;
        width: 28%;
        padding:1px 5px;
        box-sizing: initial;
    }
    ul .li img {
        width: 100%
    }
    .all_choose {
        position: absolute;
        padding:30px 0 0 30px
    }
    .okbtn {
        position: absolute;
        margin:20px 0 0 30px;
        padding: 0 50px;
        background-color:#EF5054;
        color:#fff;
        font-size: 16px;
        width: 80%;
        left:50%;
        margin-left: -40%
    }
    .checkbox {
        position: absolute;
        bottom:12px;
        right:12px
    }
</style>



