<template>
  <div id="ModifyCompany">
      <form action="/">
        <!-- <input type="text" id="cardsNum2" v-model="searchVal"  value="1" v-on:focus="focus()"> -->
        <van-search
            v-model="value"
            placeholder="填写品牌、公司简称 搜索"
            @search="onSearch"
        />
    </form>
    <div class="mycompany">
        <div class="title">我的品牌</div>
         <li v-for="(item,index) in list"
                    :key="item.name"
                    :name="item">   
                 <van-button plain round type="danger" size="small" @click="choosebrand(index)">{{ item.value }}</van-button>                             
        </li>
    
    </div>
     <van-button type="danger" size="small" class="submit" @click="submit">完成</van-button>
  </div>
</template>

<script>
export default {
  name: 'ModifyCompany',
  data () {
    return {
        value:'',
        list: [
              {"name":"0","value":"品牌1 x"},
              {"name":"1","value":"品牌2 x"},
              {"name":"2","value":"品牌3 x"},
              {"name":"3","value":"品牌4 x"},
              {"name":"4","value":"品牌5 x"},
              {"name":"5","value":"品牌6 x"},
              {"name":"6","value":"品牌7 x"},
              {"name":"7","value":"品牌8 x"},
              {"name":"8","value":"品牌9 x"}
          ]
    }
  },
  methods:{
      onSearch(){
          console.log(this.value)
          let id=this.list.length;
          let temp=[
              {"name":id,"value":this.value+" x"},
          ]
          this.list = this.list.concat(temp)
          
      },
      submit(){
          this.$router.push({path: '/'})
      },
      choosebrand(index){
         this.list.splice(index,1)
         console.log(this.list)
          //this.list = this.list.splice(index,1)
      },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    #ModifyCompany{
        position: absolute;
        left:0;
        top:0;
        width:100%;
        height:100%;
        background:#f2f2f2;
    }
    .mycompany{
        text-align: left;
        padding-left:20px;
    }
    .mycompany .title{
        padding:18px 0;
        font-size:14px;
    }
    .submit{
        width:96%;
        margin-top:30px;
    }
    li{
        list-style-type: none;
        display: inline-block;
        margin:0 10px 10px 0
    }
</style>



