            </div>
            <div slot="footer" class="footer">
                <van-button class="enter" @click="avatarDetail">点击查看</van-button>
                <van-button class="delete" @click="deleteHistory">删除</van-button>
            </div>
         </van-card>
        <van-card
            desc="创建时间：2019-09-08"
            title="习近平向2019中国国际大数据产业博览会览会致贺信致贺信"
            :thumb="imageURL"
            >
            <div slot="title" class="cardtitle">
                习近平向2019中国国际大数据产业博览会览会致贺信致贺信
            </div>
            <div slot="thumb" class="slt">
                <van-image
                    width="74"
                    height="74"   
                    fit="cover" 
                    class="img"                  
                    src="https://img.yzcdn.cn/vant/cat.jpeg"
                />
            </div>
            <div slot="desc" class="cardtime">
