  </div>
</template>

<script> 
import 'vant/lib/card/style'
import 'vant/lib/dialog/style'
export default {
  name: 'hasRead',
  data () {
    return {
      imageURL:'https://img.yzcdn.cn/vant/cat.jpeg'
    }
  },
  methods:{
      avatarDetail(){
          console.log('a')
          this.$router.push({path: '/ReadHistory'})
      },
      deleteHistory(){
        this.$dialog.alert({
            message: "确定删除该条历史记录？",
            showCancelButton: true
        })
        .then(()=>{
            console.log("点击了确认按钮噢")
        })
        .catch(()=>{
            console.log("取消")
        })
        //   Dialog.confirm({
        //     title: '标题',
        //     message: '弹窗内容'
        //     }).then(() => {
        //     // on confirm
        //     }).catch(() => {
        //     // on cancel
        //     });
      }
      
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    #hasRead{
        position: absolute;
        left:0;
        top:0;
        width:100%;
        height:100%;
        background:#f2f2f2;
    }
    .van-cell{
        margin-top:16px
    }
    .listTitle{
        text-align: left;
    }
    .listTitle .hasread span{
        color:red
    }
    .ArticleList{
        padding:10px 14px
    }
    .van-card{
        border-radius: 10px;
        margin-bottom:12px;
        padding:0;
        box-shadow: 0 0 10px  #d9dadc;
        background:#fff
    }
    .cardtitle{
        text-align: left;
        font-size:14px;
        line-height: 1.8;
        padding-top:6px
    }
    .cardtime{
        text-align:left;
        padding-top:10px;
        color:#989090
    }
    .van-card__footer{
        padding:0
    }
    .footer{
        width:100%;
        text-align: center;
        border-top: #eee solid 1px;
        color:#999999
    }
    .footer .van-button{
        margin:0;
        padding:0;
        border:none;
        width:48%;      
    }
    .footer  .enter{
        border-right: 1px solid #eee;
        color:#999
    }
    .footer .delete{
        color:#999
    }
    .slt {
        padding-top:8px;
        border-radius:20px
    }
   
</style>



