<template>
  <div id="tousu">
    <div class="cat">请选择投诉原因（可选）</div>
    <van-radio-group v-model="radio">
    <van-cell-group>
        <van-cell title="欺诈" clickable @click="radio = '1'" title-class="title">
            <van-radio slot="right-icon" name="1" checked-color="#07c160"/>
        </van-cell>
        <van-cell title="色情" clickable @click="radio = '2'" title-class="title">
            <van-radio slot="right-icon" name="2" checked-color="#07c160"/>
        </van-cell>
         <van-cell title="广告" clickable @click="radio = '3'" title-class="title">
            <van-radio slot="right-icon" name="3" checked-color="#07c160"/>
        </van-cell>
         <van-cell title="侵权" clickable @click="radio = '4'" title-class="title">
            <van-radio slot="right-icon" name="4" checked-color="#07c160"/>
        </van-cell>
         <van-cell title="诱导行为" clickable @click="radio = '5'" title-class="title">
            <van-radio slot="right-icon" name="5" checked-color="#07c160"/>
        </van-cell>
         <van-cell title="不实信息" clickable @click="radio = '6'" title-class="title">
            <van-radio slot="right-icon" name="6" checked-color="#07c160"/>
        </van-cell>
         <van-cell title="违法犯罪" clickable @click="radio = '7'" title-class="title">
            <van-radio slot="right-icon" name="7" checked-color="#07c160"/>
        </van-cell>
         <van-cell title="其他" clickable @click="radio = '8'" title-class="title">
            <van-radio slot="right-icon" name="8" checked-color="#07c160"/>
        </van-cell>
    </van-cell-group>
    </van-radio-group>
    <div class="cat">投诉描述（可选）</div>
    <van-cell-group>
        <van-field v-model="reason" placeholder="请简单描述投诉原因" rows="3"  type="textarea"/>
    </van-cell-group>
    <div class="cat">联系方式（可选）</div>
    <van-cell-group>
        <van-field v-model="weixin" placeholder="微信号，电话号码或邮箱地址" />
    </van-cell-group>
    <van-button type="primary"  class="submit">提交</van-button>
  </div>
</template>

<script>
export default {
  name: 'tousu',
  data () {
    return {
         radio: '1',
         weixin:'',
         reason:''
    }
  },
  methods:{

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    #tousu{
        position: absolute;
        left:0;
        top:0;
        width:100%;
        height:100%;
        background:#f2f2f2;
    }
    .title{
        text-align: left;
    }
    .cat{
        width:100%;
        text-align: left;
        padding:5px 0px 5px 5px;;
        color:#fff;
        background:darkgrey;
        font-size:12px;

    }
    .submit{
        margin-top:50px;
        width:96%;
        border-radius: 10px;
    }
</style>



