<template>
  <div class="Pintuku">
      <div class="gallery">       
        <p class="title">图片管理（已选择{{num}}张）</p>
        <!-- <div class="close">X</div> -->
        <div class="detail">
            <p class="lstp">历史图片</p>
            <p class="wancheng" @click="wancheng"><img src="../assets/icon_wancheng.png"/><span></span>开始拼图</p>
        </div>
        <ul>
          
          <van-checkbox-group v-model="result" :max="9" @change="getValue">
            <van-cell-group>
                <van-cell
                v-for="(item, index) in imgList"
                clickable
                :key="item"
                :name="item"
                @click="toggle(index)"
                class="li"
                >
                    <img :src="item" alt="">
                    <van-checkbox
                    :name="item"
                    ref="checkboxes"
                    checked-color="#EF5054"
                    slot="right-icon"
                    class="checkbox"
                />
             
                
                </van-cell>
            </van-cell-group>
         </van-checkbox-group>
       </ul>

      </div>  
  </div>
</template>

<script>

export default {
  name: 'Pintuku',
  data () { 
    return {
       imgList:[
           'https://linkme.oss-cn-beijing.aliyuncs.com/share/i/1.png',
           'https://linkme.oss-cn-beijing.aliyuncs.com/share/i/2.png',
           'https://linkme.oss-cn-beijing.aliyuncs.com/share/i/3.png',
           'https://linkme.oss-cn-beijing.aliyuncs.com/share/i/4.png',
           'https://linkme.oss-cn-beijing.aliyuncs.com/share/i/5.png',
           'https://linkme.oss-cn-beijing.aliyuncs.com/share/i/6.png',
           'https://linkme.oss-cn-beijing.aliyuncs.com/share/i/7.png',
           'https://linkme.oss-cn-beijing.aliyuncs.com/share/i/8.png',
           'https://linkme.oss-cn-beijing.aliyuncs.com/share/i/9.png',
           'https://linkme.oss-cn-beijing.aliyuncs.com/share/i/10.png',
      
       ],
       result: [],
       checked: false,
       num:0
    }
  },
  
  methods: {  
     okBtn() {
         this.$emit('transfer',this.content)//将值绑定到transfer上传递过去
     },
     cancelBtn() {
        this.$emit('transfer','')
     },
    toggle(index) {
      this.$refs.checkboxes[index].toggle();
    },
    getValue(e){
        this.num = e.length
    },
    allChoose(){
        var that = this
        var map1 = that.imgList.map(function (item,index) {
            return index;
        })
        var map2 = []
        if(that.checked===false){
            that.result=map2        
        }else{
            that.result=map1    
        }      
    },
    delete_img(){
        
         var item = this.result.splice(0,this.result.length)
         for(var i=0;i<item.length;i++){
             this.imgList.splice(i,1)
         }
    },
    wancheng(msg){
        console.log(msg);
        this.$emit('transfer',this.result)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .Pintuku {
        position: fixed;
        left:0;
        top:0;
        width:100%;
        height:100%;
        background:#eee;
        overflow: hidden
    }
    .gallery {
        position: relative;
        margin-top:25%;
        width: 100%;
        height: 100%;
        background-color:#fff;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        overflow: auto;  
    }
    .gallery .title {
        position: fixed;
        width: 90%;
        text-align:center;
        height:50px;
        background-color:#fff;
        left:50%;
        margin:0;
        margin-left:-45%;
        line-height: 50px;
        border-bottom:solid #d6d6d6 1px;
        z-index: 20;
    }
    .gallery .close {
        position: fixed;
        right:5%;
        margin-top:3%;
        font-size:20px;
        z-index: 20;
    }
    .gallery .detail {
        position:fixed;
        width: 100%;
        background:#fff;
        margin-top:50px;
        z-index: 20;
    }
    .gallery .bottom {
        position:fixed;
        width: 100%;
        background:#fff;
        height:80px;
        bottom:0
    }
    .lstp {
        float:left;
        color:#999;
        padding-left:20px
    }
    .wancheng {
        float:right;
        padding-right:20px;
        color:#EF5054;
        
    }
    .wancheng img {
        width: 20px;
        vertical-align: middle;
        padding-right:5px
    }
    .wancheng span {
        vertical-align: middle
    }
    ul {
        width: 100%;
        text-align: center;
        margin:100px auto 180px auto;
        z-index: 10;
    }
    ul .li {
        position: relative;
        display:inline-block;
        width: 28%;
        padding:1px 5px;
        box-sizing: initial;
    }
    ul .li img {
        width: 100%
    }
    .all_choose {
        position: absolute;
        padding:30px 0 0 30px
    }
    .delete {
        position: absolute;
        right: 15px;
        margin:20px 0 0 30px;
        padding: 0 50px;
        background-color:#EF5054;
        color:#fff;
        font-size: 16px
    }
    .checkbox {
        position: absolute;
        bottom:12px;
        right:12px
    }
</style>



