<template>
  <div id="EditCard">
      <van-cell-group>
         
        <van-cell title="头像" value="内容" >      
            <template slot="default">
                <van-uploader :after-read="modifyAvatar">
                    <van-image
                        width="40"
                        height="40"   
                        fit="cover"                   
                        :src="avatar"
                    />
                </van-uploader>
            </template>
        </van-cell>
        <van-cell title="生日" value=""  value-class="valueClass">
            <template slot="default">
                <div @click="pickdate" class="pickdate">{{ mybirthday | formateDate }} </div>
                 <!-- <van-field v-model="mybirthday" placeholder="1991-09-09" class="input" @click="pickdate"  />  -->
            </template>
             
        </van-cell>
         <van-datetime-picker
                    v-model="currentDate"
                    type="date"
                    :min-date="minDate"
                    @confirm="confirm"
                    @cancel="cancel"
                    v-if="isshow"
                    />
        <van-cell title="个人微信二维码" >
            <van-uploader
                :after-read="afterRead"
                v-model="fileList"
                :max-count="1"
                />
        </van-cell>
      </van-cell-group>
      <van-cell-group>
        <van-cell title="姓名" value="我的名字" value-class="valueClass">
            <template slot="default">
                <van-field v-model="myname" placeholder="我的姓名" class="input" input-align="right"/>
            </template>
            <template slot="title">
                <div class="title"><span>*</span>姓名</div>
            </template>
        </van-cell>
        <van-cell title="公司" value="请填写公司" value-class="valueClass">
            <template slot="title">
                <div class="title">公司</div>
            </template>
             <template slot="default">
                <van-field v-model="mycompany" placeholder="填写公司" class="input" input-align="right"/>
            </template>
        </van-cell>
        <van-cell title="职位" value="请填写职位" value-class="valueClass">
            <template slot="default">
                <van-field v-model="myjob" placeholder="填写职位" class="input" input-align="right"/>
            </template>
        </van-cell>
        <van-cell title="手机号码" value="请填写手机号" value-class="valueClass">
            <template slot="title">
                <div class="title"><span>*</span>手机号码</div>
            </template>
            <template slot="default">
                <van-field v-model="mymobile" placeholder="手机号码" class="input" input-align="right"/>
            </template>
        </van-cell>
        <van-cell title="座机" value="请填写座机" value-class="valueClass">
            <template slot="default">
                <van-field v-model="myphone" placeholder="填写座机" class="input" input-align="right"/>
            </template>
        </van-cell>
        <van-cell title="微信号" value="请填写微信号" value-class="valueClass">
            <template slot="default">
                <van-field v-model="myweixin" placeholder="微信号" class="input" input-align="right"/>
            </template>
        </van-cell>
        <van-cell title="QQ号" value="请填写QQ号" value-class="valueClass">
            <template slot="default">
                <van-field v-model="myqq" placeholder="qq号" class="input" input-align="right"/>
            </template>
        </van-cell>
      </van-cell-group>
    
      <div>
          <van-button round type="danger" class="ok" @click="okbtn">确认修改</van-button>
      </div>
     
  </div>
</template>

<script>
import 'vant/lib/switch/style'
import 'vant/lib/uploader/style'
import 'vant/lib/datetime-picker/style'
import 'vant/lib/toast/style'

export default {
  name: 'EditCard',
  data () {
    return {
      checked1: false,
      checked2: true,
      checked3: true,
      fileList: [],
      isshow:false,
      myname:'',
      mybirthday:'1980-01-01',
      mycompany:'',
      myjob:'',
      mymobile:'',
      myphone:'',
      myweixin:'',
      myqq:'',
      minDate: new Date(1900, 10, 1),
      maxDate: new Date(),
      currentDate: new Date(1970,1,1),
      avatar:'https://img.yzcdn.cn/vant/cat.jpeg'
    }
  },
   filters: {
       formateDate:function(time) {
        var date=new Date(time);
        var year=date.getFullYear();
        var month= date.getMonth()+1<10 ? "0"+(date.getMonth()+1) : date.getMonth()+1;
        var day=date.getDate()<10 ? "0"+date.getDate() : date.getDate();
        var hours=date.getHours()<10 ? "0"+date.getHours() : date.getHours();
        var minutes=date.getMinutes()<10 ? "0"+date.getMinutes() : date.getMinutes();
        var seconds=date.getSeconds()<10 ? "0"+date.getSeconds() : date.getSeconds();
        // 拼接
        return year+"-"+month+"-"+day
    }
  },
  methods: {
      afterRead(file) {
      // 此时可以自行将文件上传至服务器
      console.log(file);
    },
    okbtn() {
        if(this.myname == ''){
            this.$toast('姓名不能为空')
        }else if(this.mymobile == ''){
            this.$toast('手机号码不能为空')
        }else {
            this.$toast('成功')
        }
         
         //this.$router.go(-1)
    },
    confirm(){
       this.isshow=false
       console.log(this.currentDate)
       this.mybirthday = this.currentDate
    },
    cancel(){
        this.isshow=false
    },
    pickdate(){
        this.isshow=true
    },
    modifyAvatar(file) {
        this.avatar = file.content
    }
  }
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    #EditCard{
        position: absolute;
        left:0;
        top:0;
        width:100%;
        height:100%;
        background:#f2f2f2;
    }
    .van-cell__title{
        text-align: left;
        font-size:16px
    }
    .van-cell-group{
        margin-bottom:8px
    }
    p{
        color:#999;
        font-size:12px
    }
    .p1,.p2{
        text-align: right;
        padding-right:10px
    }
    .p3{
        padding:0;
        margin-bottom:-10px
    }
    .p3 span{
        color:#ef5054
    }
    .p4 span{
        color:#ef5054
    }
    .topshow{
        min-width: 260px
    }
    .ok{
        position:absolute;
        bottom:20px;
        width:90%;
        left:50%;
        margin-left:-45%
    }
    .title span{
        color:red;
        padding-right:6px
    }
    .valueClass{
        width: 32%;
        position: absolute;
        right: 0;
        top: 0;
    }
    .pickdate{
        padding:10px 10px;
    }
    .van-image{
        overflow: hidden;
        border-radius:30px
    }
</style>



